import * as React from "react";
import { Label, Input } from "reactstrap";
import { InputType } from "reactstrap/es/Input";

interface IProps {
  id?: string;
  name?: string;
  label?: string;
  placeHolder?: string;
  error?: boolean;
  helperText?: string;
  type?: InputType;
  disabled?: boolean;
  onchange?: any;
  classNameTextBox?: string;
  value?: string;
  pattern?: string;
  required?: boolean;
}

const getStyleClass = (isError: boolean) => {
  let styleClass: string = "custom-text-box";
  if (isError) {
    return `${styleClass} custom-text-box-input-error`
  }
  return `${styleClass} custom-text-box-input-default`
}

const CustomTextBox: React.FunctionComponent<IProps> = ({
  id,
  name,
  label,
  placeHolder,
  error,
  helperText,
  disabled,
  onchange,
  type,
  classNameTextBox,
  value,
  pattern,
  required,
}: IProps): JSX.Element => {
  return (
    <div>
      {label ? <Label>{label}</Label> : undefined}
      <Input
        id={id ? id : ""}
        name={name ? name : ""}
        className={`${error ? getStyleClass(error) : getStyleClass(false)} ${
          classNameTextBox ? classNameTextBox : ""
        } `}
        defaultValue={value ? value : ""}
        placeholder={placeHolder ? placeHolder : ""}
        disabled={disabled === true ? disabled : false}
        onChange={onchange ? onchange : undefined}
        type={type ? type : "text"}
        pattern={pattern ? pattern : ".*"}
        required={required ? required : false}
      />
      {helperText ? (
        <div
          className={
            (error
              ? "custom-text-box-helper-text-error"
              : "custom-text-box-helper-text-default") +
            " custom-text-box-helper-text"
          }
        >
          {helperText}
        </div>
      ) : undefined}
    </div>
  );
};
export { CustomTextBox };
