import React, {useEffect, useRef, useState} from 'react';

interface IProps {
    className?: string;
    isDisabled?: boolean;
    defaultDate?: Date;
    onChange?: any;
}

const DatePicker: React.FC<IProps> = ({
                                          className,
                                          isDisabled,
                                          defaultDate,
                                          onChange,
                                      }) => {
    let oneDay = 60 * 60 * 24 * 1000;
    let todayTimestamp = Date.now() - (Date.now() % oneDay) + (new Date().getTimezoneOffset() * 1000 * 60);
    let daysMap = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    let monthMap = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];


    let date = new Date();
    const [yearState, setYearState] = useState(date.getFullYear());
    const [monthState, setMonthState] = useState(date.getMonth());
    const [showDatePickerComponent, setShowDatePicker] = useState(false);
    const [selectedDay, setSelectedDate] = useState(todayTimestamp);
    const [monthDetail, setMonthDetail] = useState(getMonthDetails(yearState, monthState));
    const [inputValue, setInput] = useState("");
    const myRef = useRef<HTMLInputElement>(null);

    if (defaultDate) {
        setSelectedDate(defaultDate.getTime);
    }

    function getMonthDetails(year: number, month: number) {
        let firstDay = (new Date(year, month)).getDay();
        let numberOfDays = getNumberOfDays(year, month);
        let monthArray = [];
        let rows = 6;
        let currentDay = null;
        let index = 0;
        let cols = 7;

        for (let row = 0; row < rows; row++) {
            for (let col = 0; col < cols; col++) {
                currentDay = getDayDetails({
                    index,
                    numberOfDays,
                    firstDay,
                    year,
                    month
                });
                monthArray.push(currentDay);
                index++;
            }
        }
        return monthArray;
    }

    function getNumberOfDays(year: number, month: number) {
        return 40 - new Date(year, month, 40).getDate();
    }

    function getDayDetails(arg0: { index: number; numberOfDays: number; firstDay: number; year: number; month: number; }): any {
        let date = arg0.index - arg0.firstDay;
        let day = arg0.index % 7;
        let prevMonth = arg0.month - 1;
        let prevYear = arg0.year;
        if (prevMonth < 0) {
            prevMonth = 11;
            prevYear--;
        }

        let prevMonthNumberOfDays = getNumberOfDays(prevYear, prevMonth);
        let _date = (date < 0 ? prevMonthNumberOfDays + date : date % arg0.numberOfDays) + 1;
        let month = date < 0 ? -1 : date >= arg0.numberOfDays ? 1 : 0;
        let timestamp = new Date(arg0.year, arg0.month, _date).getTime();
        return {
            date: _date,
            day,
            month,
            timestamp,
            dayString: daysMap[day]
        }
    }

    function showDatePicker(isShow: boolean): void {
        setShowDatePicker(isShow);
        setInput(getDateStringFromTimestamp(selectedDay));
    }

    function updateDateFromInput() {
        let dateData = getDateFromDateString(inputValue);
        if (dateData !== null) {
            setDate(dateData);
            setYearState(dateData.year);
            setMonthState(dateData.month - 1);
            setMonthDetail(getMonthDetails(dateData.year, dateData.month - 1));
        }
    }


    function useOutsideAlerter(ref: React.RefObject<HTMLInputElement>) {
        useEffect(() => {
            /**
             * Alert if clicked on outside of element
             */
            function handleClickOutside(event: { target: any; }) {
                if (ref.current && !ref.current.contains(event.target)) {
                    setShowDatePicker(false);
                }
            }

            // Bind the event listener
            document.addEventListener("mousedown", handleClickOutside);
            return () => {
                // Unbind the event listener on clean up
                document.removeEventListener("mousedown", handleClickOutside);
            };
        }, [ref]);
    }

    useOutsideAlerter(myRef); //trigger the outside click event


    /**
     *  Core
     */


    const isCurrentDay = (day: { timestamp: number; }) => {
        return day.timestamp === todayTimestamp;
    }

    const isSelectedDay = (day: { month: any; timestamp: number; }) => {
        if (day.month === 0) {          // enabled
            return day.timestamp === selectedDay;
        }
        return false;
    }

    const getDateFromDateString = (dateValue: string) => {
        let dateData = dateValue.split('-').map(d => parseInt(d, 10));
        if (dateData.length < 3)
            return null;

        let year = dateData[0];
        let month = dateData[1];
        let date = dateData[2];
        return {year, month, date};
    }

    const getMonthStr = (month: number) => monthMap[Math.max(Math.min(11, month), 0)] || 'Month';

    const getDateStringFromTimestamp = (timestamp: string | number | Date) => {
        let dateObject = new Date(timestamp);
        let month = dateObject.getMonth() + 1;
        let date = dateObject.getDate();
        return dateObject.getFullYear() + '-' + (month < 10 ? '0' + month : month) + '-' + (date < 10 ? '0' + date : date);
    }

    const setDateToInput = (timestamp: string | number | Date) => {
        let dateString = getDateStringFromTimestamp(timestamp);
        setInput(dateString);
    }

    const setDate = (dateData: { year: any; month: any; date: any; }) => {
        let selectedDay = new Date(dateData.year, dateData.month - 1, dateData.date).getTime;
        setSelectedDate(selectedDay);
    }

    const onDateClick = (day: { timestamp: any; }) => {
        setSelectedDate(day.timestamp)
        setDateToInput(day.timestamp);
    }

    const setYear = (offset: number) => {
        let newYear = yearState + offset;
        let newMonth = monthState;
        setYearState(newYear);
        setMonthState(newMonth);
        setMonthDetail(getMonthDetails(newYear, newMonth));
    }

    const setMonth = (offset: number) => {
        let newYear = yearState;
        let newMonth = monthState + offset;
        if (newMonth === -1) {
            newMonth = 11;
            newYear--;
        } else if (newMonth === 12) {
            newMonth = 0;
            newYear++;
        }
        setYearState(newYear);
        setMonthState(newMonth);
        setMonthDetail(getMonthDetails(newYear, newMonth));
    }

    const executeOnClickFunction = (day: { timestamp: any; }, onChange: any) => {
        onDateClick(day)
        if (onChange) {
            onChange();
        }
    }

    /**
     *  Renderers
     */

    const renderCalendar = () => {
        let days = monthDetail.map((day, index) => {
            return (
                <div className={'c-day-container ' + (day.month !== 0 ? ' disabled' : '') +
                (isCurrentDay(day) ? ' highlight' : '') + (isSelectedDay(day) ? ' highlight-green' : '')} key={index}>
                    <div className='cdc-day'>
                        <span onClick={() => executeOnClickFunction(day, onChange)}>
                            {day.date}
                        </span>
                    </div>
                </div>
            )
        })

        return (
            <div className='c-container'>
                <div className='cc-head'>
                    {['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'].map((d, i) => <div key={i}
                                                                                          className='cch-name'>{d}</div>)}
                </div>
                <div className='cc-body'>
                    {days}
                </div>
            </div>
        )
    }


    return (
        <div className='MyDatePicker' ref={myRef}>
            <div className='mdp-input' onClick={() => showDatePicker(true)}>
                <input type='date' className={className} onChange={updateDateFromInput} disabled={isDisabled}
                       value={inputValue}/>
            </div>
            {showDatePickerComponent ? (
                <div className='mdp-container'>
                    <div className='mdpc-head'>
                        <div className='mdpch-button'>
                            <div className='mdpchb-inner' onClick={() => setYear(-1)}>
                                <span className='mdpchbi-left-arrows'></span>
                            </div>
                        </div>
                        <div className='mdpch-button'>
                            <div className='mdpchb-inner' onClick={() => setMonth(-1)}>
                                <span className='mdpchbi-left-arrow'></span>
                            </div>
                        </div>
                        <div className='mdpch-container'>
                            <div className='mdpchc-year'>{yearState}</div>
                            <div className='mdpchc-month'>{getMonthStr(monthState)}</div>
                        </div>
                        <div className='mdpch-button'>
                            <div className='mdpchb-inner' onClick={() => setMonth(1)}>
                                <span className='mdpchbi-right-arrow'></span>
                            </div>
                        </div>
                        <div className='mdpch-button' onClick={() => setYear(1)}>
                            <div className='mdpchb-inner'>
                                <span className='mdpchbi-right-arrows'></span>
                            </div>
                        </div>
                    </div>
                    <div className='mdpc-body'>
                        {renderCalendar()}
                    </div>
                </div>
            ) : ''}
        </div>
    )
};

export default DatePicker;