import * as React from 'react';
import OtpInput from 'react-otp-input';
import { Container } from 'reactstrap';
import Countdown from 'react-countdown';
import { AuthService, AuthServiceImpl } from '../../../../service/auth.service';
import { ButtonType, CustomButton, CustomLabel, LabelColors, LabelTypes, showToast, TOASTTYPE } from '../../../atoms';
import {AuthFormType} from '../../auth-modal';
import {getPhoneNumberWithCountryCode} from "../../../../commons/util/validation";


interface OTPFormProps {
    setUserShowName: Function;
    changeModalType: Function;
    setIsLoggedIn: Function;
    phoneNumber?: string;
    password?: string;
}

const CustomOTPForm: React.FunctionComponent<OTPFormProps> = ({ changeModalType, phoneNumber, password, setUserShowName, setIsLoggedIn }): JSX.Element => {

    const [otpValueState, setOtpValueState] = React.useState("");
    const [resetButtonState, setResetButtonState] = React.useState(false);

    const authService: AuthService = new AuthServiceImpl();

    const submitOTPTrigger = async () => {
        if (phoneNumber && password) {
            const phoneNumberWithCountryCode: string = getPhoneNumberWithCountryCode(phoneNumber);
            authService.submitOTP(phoneNumberWithCountryCode, otpValueState)
                .then( async ({error} )=>{
                    if (error){
                        console.error(`${error.message} code=${error.code} : phoneNumber=${phoneNumberWithCountryCode}, otp=${otpValueState}`);
                        if (error.code==="OTP_EXPIRED"){
                            setResetButtonState(true);
                            showToast(`OTP expired. Try again !`, TOASTTYPE.error);
                            return;
                        }
                        showToast(`${error.message}. Try again !`, TOASTTYPE.error);
                        return;
                    }
                    // need to login after successful user verification, otherwise user not authenticated error when get user
                    authService.logIn(phoneNumberWithCountryCode,password)
                        .then(async ({error})=>{
                            if (error){
                                showToast(`${error.message}. Please login again !`, TOASTTYPE.error);
                                return;
                            }
                            setUserShowName(await authService.getUserShowName());
                            setIsLoggedIn(true);
                            changeModalType(AuthFormType.REGISTER_SUCCESS);
                        });
                });
        } else {
            console.warn("Insufficient details");
            showToast("Insufficient details to submit OTP. Try again !", TOASTTYPE.error);
        }
    }

    const resendOTPTrigger = () => {
        if (phoneNumber) {
            authService.sendOTP(getPhoneNumberWithCountryCode(phoneNumber))
                .then( ({error} )=>{
                    if (error){
                        console.error(`${error.message} code=${error.code} : phoneNumber=${phoneNumber}`);
                        showToast(`${error.message}. Try again !`, TOASTTYPE.error);
                        return;
                    }
                    setResetButtonState(false);
                });

        } else {
            console.warn(`Phone number not found : phoneNumber=${phoneNumber}`);
            showToast("Phone number not found. Try again !", TOASTTYPE.error);
        }
    }

    const validateOTPRealTime = () => {
        return otpValueState.length === 4;

    }

    const renderer = ({ minutes,seconds, completed }: any) => {
        if (completed) {
            return <p className="otp-countdown-expired">OTP expired. Try again with resend.</p>;
        } else {
            return <span className="otp-countdown-seconds">{}{minutes}:{seconds}</span>;
        }
    };

    // TODO - style the otp form
    return (
        <Container className="form-container">
            <CustomLabel
                children={"Submit OTP"}
                type={LabelTypes.H5}
                color={LabelColors.DARKGREY}
            />
            <hr />
            <div className="otp-box-container">
                <OtpInput
                    numInputs={4}
                    value={otpValueState}
                    onChange={setOtpValueState}
                    separator={<span>-</span>}
                    inputStyle={{
                        width: '3rem',
                        height: '3rem',
                        margin: '20px 1rem',
                        fontSize: '1rem',
                        borderRadius: 0
                    }}
                />
            </div>
            <div className="otp-countdown-container">
                <Countdown date={Date.now() + 90000} renderer={renderer} />
            </div>
            <div className="otp-button-container">
                <CustomButton disabled={!resetButtonState} className="otp-button" onClick={resendOTPTrigger} title="Resend" type={ButtonType.PRIMARY} />
                <CustomButton className="otp-button" onClick={submitOTPTrigger} title="Submit" type={ButtonType.PRIMARY} disabled={!validateOTPRealTime()} />
            </div>

        </Container>
    )

}

export default CustomOTPForm;