import * as React from 'react';
import { Container } from 'reactstrap';
import { AuthService, AuthServiceImpl } from '../../../../service/auth.service';
import { ButtonType, CustomButton, CustomLabel, LabelTypes, showToast, TOASTTYPE, CustomTextBox, LabelColors } from '../../../atoms';
import { AuthFormType } from '../../auth-modal';

interface ForgotPasswordFormProps {
    changeModalType: Function;
    closeModalAction: Function;
}

const ForgetPasswordForm: React.FunctionComponent<ForgotPasswordFormProps> = ({ changeModalType, closeModalAction }): JSX.Element => {

    const [username, setUsername] = React.useState("");
    const [code, setcode] = React.useState("");
    const [password, setpassword] = React.useState("");
    const [showPasswordSubmit, setShowPasswordSubmit] = React.useState(false);

    const authService: AuthService = new AuthServiceImpl();

    const forgotPassworRequesTrigger = async () => {
        let phoneNumberWithCountryCode = "+94" + username.substring(1);
        authService.forgotPasswordRequest(phoneNumberWithCountryCode)
            .then(r => {
                setShowPasswordSubmit(true);
            })
            .catch(e => {
                showToast("Verification code sending failed", TOASTTYPE.error);
            })
    }

    const forgotPassworSubmitTrigger = async () => {
        let phoneNumberWithCountryCode = "+94" + username.substring(1);
        authService.forgotPasswordResponse(phoneNumberWithCountryCode, code, password)
            .then(r => {
                closeModalAction(AuthFormType.REGISTER_SUCCESS);
            })
            .catch(e => {
                showToast("Forgot password failed", TOASTTYPE.error);
            })
    }

    if (showPasswordSubmit) {
        return (
            <div className="form">
                <Container className="form-container">
                    <CustomLabel
                        children={"Forgot password"}
                        type={LabelTypes.H5}
                        color={LabelColors.DARKGREY}
                    />
                    <hr />
                    <br />
                    <form>
                        <CustomTextBox classNameTextBox="form-text-box" placeHolder="Verification code" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { setcode(e.target.value) }} />
                        <br />
                        <CustomTextBox classNameTextBox="form-text-box" placeHolder="Password" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { setpassword(e.target.value) }} />
                        <br />
                        <CustomButton className="form-button-submit" onClick={forgotPassworSubmitTrigger} title="Set password" type={ButtonType.PRIMARY} />
                    </form>
                </Container>
                <br />
            </div>
        )
    } else {
        return (
            <div className="form">
                <Container className="form-container">
                    <CustomLabel
                        children={"Forgot password"}
                        type={LabelTypes.H5}
                        color={LabelColors.DARKGREY}
                    />
                    <hr />
                    <br />
                    <form>
                        <CustomTextBox classNameTextBox="form-text-box" placeHolder="Username" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { setUsername(e.target.value) }} />
                        <br />
                        <CustomButton className="form-button-submit" onClick={forgotPassworRequesTrigger} title="Set password" type={ButtonType.PRIMARY} />
                    </form>
                </Container>
                <br />
            </div>
        )
    }

}

export default ForgetPasswordForm;