import * as React from 'react';
import { Container } from 'reactstrap';
import { Icon } from '@iconify/react';
import facebook from '@iconify-icons/logos/facebook';
import googleGmail from '@iconify-icons/logos/google-gmail';

import {AuthFormType} from '../../auth-modal';
import { getPhoneNumberWithCountryCode, isPhoneNumberValid } from '../../../../commons/util/validation'
import { ButtonType, CustomButton, CustomLabel, LabelTypes, CustomTextBox, showToast, TOASTTYPE, LabelColors } from '../../../atoms';
import { AuthService, AuthServiceImpl } from '../../../../service/auth.service';

interface RegisterFormProps {
    changeModalType: Function;
    setPhoneNumberState: Function;
    setPasswordState: Function;
    phoneNumberState: string|undefined;
    passwordState: string|undefined;
}

const CustomRegisterForm: React.FunctionComponent<RegisterFormProps> = ({ changeModalType, setPhoneNumberState, setPasswordState, phoneNumberState, passwordState }): JSX.Element => {

    const [name, setName] = React.useState("");
    const [Confirmpassword, setConfirmPassword] = React.useState("");

    const authService: AuthService = new AuthServiceImpl();

    const onPhoneNumberChange = (phoneNumber:string) => {
        setPhoneNumberState(phoneNumber);
    }

    const onPasswordChange = (password:string) => {
        setPasswordState(password);
    }

    const onNameChange = (name:string) => {
        setName(name);
    }

    const validateFormRealTime = () => {
        if (!(passwordState && Confirmpassword && name && phoneNumberState)) {
            return false;
        } else if (passwordState === "" || Confirmpassword === "" || name === "" || phoneNumberState === "") {
            return false;
        } else if (!(passwordState === Confirmpassword)) {
            return false;
        } else if (!isPhoneNumberValid(phoneNumberState)) {
            return false;
        }
        return true;
    }

    const userRegistrationTrigger = (e: React.ChangeEvent<HTMLButtonElement>) => {
        e.preventDefault();
        if (phoneNumberState && passwordState) {
            let phoneNumberWithCountryCode: string = getPhoneNumberWithCountryCode(phoneNumberState);
            authService.register(phoneNumberWithCountryCode, passwordState, name)
                .then(({ error }) => {
                    if (error) {
                        console.error("USER REGISTRATION FAILED");
                        showToast(error.message, TOASTTYPE.error);
                    } else {
                        console.log("USER REGISTRATION PROCEED TO OTP");
                        changeModalType(AuthFormType.OTP);
                    }
                })
                .catch(() => {
                    console.error("USER REGISTRATION FAILED");
                    showToast("Unexpected error occured! Try again!", TOASTTYPE.error);
                });
        } else {
            console.warn("Insufficient details");
            showToast("Insufficient details to submit OTP. Try again !", TOASTTYPE.error);
        }
    }


    return (
        <div className="form">
            <Container className="form-container">
                <CustomLabel
                    children={"Register in CakeHub"}
                    type={LabelTypes.H5}
                    color={LabelColors.DARKGREY}
                />
                <hr />
                <br />
                <form>
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Name" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { onNameChange(e.target.value) }} />
                    <br />
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Phone number" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { onPhoneNumberChange(e.target.value) }} />
                    <br />
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Password" type={"password"} onchange={(e: React.ChangeEvent<HTMLInputElement>) => { onPasswordChange(e.target.value) }} helperText={"Helper text for the password"} />
                    <br />
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Confirm password" type={"password"} onchange={(e: React.ChangeEvent<HTMLInputElement>) => { setConfirmPassword(e.target.value) }} />
                    <br />
                    <CustomButton className="form-button-submit" onClick={userRegistrationTrigger} title="Register" type={ButtonType.PRIMARY} disabled={!validateFormRealTime()} />
                    <br />
                    <div>
                        <span>
                            <p className="form-text">Already have an account on CakeHub ?</p> <p onClick={() => { changeModalType(AuthFormType.LOGIN) }} className="form-link-text">Log in !</p>
                        </span>
                        <br />
                    </div>
                    <div className="form-federated-row">
                        <button className="form-icon-btn btn" onClick={(e) => {
                            e.preventDefault();
                            authService.facebookSignIn();
                        }}>
                            <Icon icon={facebook} height="50px" width="50px" />
                        </button>
                        <button className="form-icon-btn btn" onClick={(e) => {
                            e.preventDefault();
                            authService.googleSignIn();
                        }}>
                            <Icon icon={googleGmail} height="50px" width="50px" />
                        </button>
                    </div>
                </form>
            </Container>
            <br />
        </div>
    );
};

export default CustomRegisterForm;