import * as React from "react";
import { Button } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { IconDefinition } from "@fortawesome/free-solid-svg-icons";
import { SizeProp } from "@fortawesome/fontawesome-svg-core";

/**
 * Props accepted by button atom.
 */
interface IProps {
  title?: string;
  style?: React.CSSProperties;
  block?: boolean;
  onClick?: (event: any) => void;
  outline?: boolean;
  className?: string;
  disabled?: boolean;
  type?: ButtonType;
  size?: string;
  icon?: IconDefinition;
  iconClassName?: string;
  iconSize?: SizeProp;
  color?: string;
}

export enum ButtonType {
  PRIMARY = "primary",
  SECONDARY = "secondary",
  LINK = "link",
}

const getStyles = (
  type?: ButtonType,
  disabled?: boolean,
) => {
  let styles = "responsive-btn ";
  if (disabled) {
    styles = "responsive-btn button-disable ";
  }
  switch (type) {
    case ButtonType.PRIMARY:
      styles += " btn-primary ";
      break;
    case ButtonType.LINK:
      styles += " btn-link ";
      break;
    case ButtonType.SECONDARY:
      styles += " btn-secondary ";
      break;
    default:
      styles += " btn-secondary ";
      break;
  }
  return styles;
};

export const CustomButton: React.FunctionComponent<IProps> = ({
  title,
  style,
  block,
  onClick,
  outline,
  className,
  disabled,
  type,
  size,
  icon,
  iconClassName,
  iconSize,
  color,
}: IProps): JSX.Element => {
  return (
    <Button
      size={size}
      outline={outline}
      block={block}
      style={style}
      onClick={onClick}
      disabled={disabled}
      color={color ? color : type}
      className={`button ${getStyles(type, disabled)} ${className ? className : ""}`}
    >
      {title}
      {icon ? (
        <FontAwesomeIcon
          className={`ml-2 ${iconClassName ? iconClassName : ""}`}
          icon={icon}
          size={iconSize ? iconSize : "sm"}
        />
      ) : null}
    </Button>
  );
};
