import styles from "./index.module.css";
import React, {useState} from "react";
import {useSelector} from "react-redux";
import {userDetail} from "../../../store/user/selectors";
import {Col, Row} from "reactstrap";
import {InformationCard} from "../";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheckCircle} from "@fortawesome/free-solid-svg-icons";
import {CustomTextBox, CustomButton, CustomLabel, LabelColors, LabelTypes} from "../../atoms";
import { BootstrapTable, TableHeaderColumn} from "react-bootstrap-table";
import {sampleCake} from "../../../assets";

const UserProfile:React.FunctionComponent = (): JSX.Element =>{
    const userDetails: any = useSelector(userDetail);
    const [name, setName] = useState(userDetails.userDetails.name);
    const [email, setEmail] = useState(userDetails.userDetails.email);
    const [emailVerified, setEmailVerified] = useState(userDetails.userDetails.emailVerified);
    const [mobile, setMobile] = useState(userDetails.userDetails.mobile);
    const [mobileVerified, setMobileVerified] = useState(userDetails.userDetails.mobileVerified);
    const [defaultAddressName, setDefaultAddressName] = useState(userDetails.userDetails.defaultAddress.split(",")[0]);
    const [defaultAddress, setDefaultAddress] = useState(userDetails.userDetails.defaultAddress.split(defaultAddressName+",")[1]);
    const [defaultAddressMobile, setDefaultAddressMobile] = useState(userDetails.userDetails.defaultAddressMobile);
    const [deliveryAddressName, setDeliveryAddressName] = useState(userDetails.userDetails.deliveryAddress.split(",")[0]);
    const [deliveryAddress, setDeliveryAddress] = useState(userDetails.userDetails.deliveryAddress.split(deliveryAddressName+",")[1]);
    const [deliveryAddressMobile, setDeliveryAddressMobile] = useState(userDetails.userDetails.deliveryAddressMobile);
    const [orderHistory, setOrderHistory] = useState(userDetails.userDetails.orderHistory);

    const [currentPw, setCurrentPw] = useState('');
    const [newPw, setNewPw] = useState('');
    const [newPwRepeat, setNewPwRepeat] = useState('');

    function imageFormatter(cell: any, row: any){
        // ToDo After completing the backend part of 'fetching order history', line-34 can be uncommented and line-35 can be removed
        // return (<img src={cell} alt="sample-cake" height={130} width={130} />);
        return (<img src={sampleCake} alt="sample-cake" height={50} width={50} />);
    }

    function linkFormatter(cell: any, row: any){
        console.log(row.orderId);
        return (
            <div>
                <a href="">
                    <div className={styles.manage_class}>
                        Manage
                    </div>
                </a>
            </div>
        );
    }

    let returnElement :JSX.Element = <div/>;
    returnElement= (
        <div className={styles.whole_page}>
            <div>
                <CustomLabel
                    children="My Account"
                    type={LabelTypes.H2}
                    color={LabelColors.DARKGREY}
                    bold={true}
                />
            </div>
            <Row>
                <Col sm="6" className={styles.padding_top_bottom}>
                    <div className="w-100">
                        <InformationCard heading={"Personal Details"}>
                            <form>
                                <Row>
                                    <Col sm="7">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={`${styles.name} ${styles.profile_line_box}`} value={name} type={"text"} placeHolder="Name" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setName(e.target.value.trim());
                                            }} />
                                        </div>
                                    </Col>
                                    <Col sm="1">
                                    </Col>
                                    <Col sm="4">
                                        <div className={styles.save_button}>
                                            <CustomButton disabled={
                                                userDetails.userDetails.name === name
                                            } title="Save" />
                                        </div>
                                    </Col>
                                </Row>
                            </form>
                            <form>
                                <Row>
                                    <Col sm="7">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={`${styles.profile_line_box} ${styles.email}`} value={email} type={"email"} placeHolder="Email" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setEmail(e.target.value.trim()) ;
                                                if(userDetails.userDetails.email !== e.target.value.trim()){
                                                    setEmailVerified(false);
                                                } else {
                                                    setEmailVerified(true);
                                                }
                                            }} />
                                        </div>
                                    </Col>
                                    <Col sm="1">
                                        <FontAwesomeIcon
                                            icon={faCheckCircle}
                                            className={`verified_icon ${emailVerified ? "active" : ""}`}
                                        />
                                    </Col>
                                    <Col sm="4">
                                        <div className={styles.save_button}>
                                            <CustomButton disabled={
                                                userDetails.userDetails.email === email
                                            } title="Verify" />
                                        </div>
                                    </Col>
                                </Row>
                            </form>
                            <form>
                                <Row>
                                    <Col sm="7">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={styles.profile_line_box} value={mobile} placeHolder="Mobile" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setMobile(e.target.value.trim());
                                                if(userDetails.userDetails.mobile !== e.target.value.trim()){
                                                    setMobileVerified(false);
                                                } else {
                                                    setMobileVerified(true);
                                                }
                                            }} />
                                        </div>
                                    </Col>
                                    <Col sm="1">
                                        <FontAwesomeIcon
                                            icon={faCheckCircle}
                                            className={`${styles.verified_icon} ${mobileVerified ? styles.active : ""}`}
                                        />
                                    </Col>
                                    <Col sm="4">
                                        <div className={styles.save_button}>
                                            <CustomButton disabled={
                                                userDetails.userDetails.mobile === mobile
                                            } title="Send OTP" />
                                        </div>
                                    </Col>
                                </Row>
                            </form>
                        </InformationCard>
                    </div>
                </Col>
                <Col sm="6" className={styles.padding_top_bottom}>
                    <div className="w-100">
                        <InformationCard heading={"Change Password"}>
                            <form>
                                <Row>
                                    <Col sm="9">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={styles.profile_line_box} type={"password"} placeHolder="Current Password" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setCurrentPw(e.target.value.trim());
                                            }} />
                                        </div>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col sm="9">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={styles.profile_line_box} type={"password"} placeHolder="New Password" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setNewPw(e.target.value.trim()) ;
                                            }} />
                                        </div>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col sm="9">
                                        <div className={styles.profile}>
                                            <CustomTextBox classNameTextBox={styles.profile_line_box} type={"password"} placeHolder="Confirm Password" onchange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                setNewPwRepeat(e.target.value.trim());
                                            }} />
                                        </div>
                                    </Col>
                                    <Col sm="3">
                                        <div className={styles.save_button}>
                                            <CustomButton disabled={
                                                currentPw === '' || newPw === '' || newPwRepeat === ''
                                            } title="Save" />
                                        </div>
                                    </Col>
                                </Row>
                            </form>
                        </InformationCard>
                    </div>
                </Col>
            </Row>
            <div className={styles.padding_top_bottom}>
                <div>
                    <div className="w-100 p-3 info-card">
                        <div className={styles.address_book_header}>
                            <Row>
                                <CustomLabel
                                    type={LabelTypes.H2}
                                    color={LabelColors.DARKGREY}
                                    bold={true}
                                    children={"Address Book | "}
                                />
                                <a href="">
                                    <div className={styles.edit_class}>
                                        Edit
                                    </div>
                                </a>
                            </Row>
                        </div>
                        <Row>
                            <Col sm="5">
                                <div className={styles.address_book_content}>
                                    <CustomLabel
                                        type={LabelTypes.H6}
                                        color={LabelColors.DARKGREY}
                                        bold={true}
                                        children={"Default Delivery Address"}
                                    />
                                    <br/>
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={true}
                                        children={defaultAddressName}
                                    />
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={false}
                                        children={defaultAddress}
                                    />
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={false}
                                        children={defaultAddressMobile}
                                    />
                                </div>
                            </Col>
                            <div className={styles.vl}/>
                            <Col sm="5">
                                <div className={styles.address_book_content}>
                                    <CustomLabel
                                        type={LabelTypes.H6}
                                        color={LabelColors.DARKGREY}
                                        bold={true}
                                        children={"Default Billing Address"}
                                    />
                                    <br/>
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={true}
                                        children={deliveryAddressName}
                                    />
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={false}
                                        children={deliveryAddress}
                                    />
                                    <CustomLabel
                                        type={LabelTypes.H5}
                                        color={LabelColors.DARKGREY}
                                        bold={false}
                                        children={deliveryAddressMobile}
                                    />
                                </div>
                            </Col>
                        </Row>
                    </div>
                </div>
            </div>
            <div className="w-100">
                <InformationCard heading={"Order History"}>
                    <div className={styles.main_table_container}>
                        <BootstrapTable className={styles.main_table} data={ orderHistory } bordered={ false } version="4" pagination>
                            <TableHeaderColumn className={styles.table_rows} dataField='orderId' isKey={ true } dataAlign='center'>Order #</TableHeaderColumn>
                            <TableHeaderColumn className={styles.table_rows} dataField='placedOn' dataAlign='center'>Placed on</TableHeaderColumn>
                            <TableHeaderColumn className={styles.table_rows} dataField='itemImage' dataAlign='center' dataFormat={imageFormatter}>Items</TableHeaderColumn>
                            <TableHeaderColumn className={styles.table_rows} dataField='total' dataAlign='center'>Total</TableHeaderColumn>
                            <TableHeaderColumn className={styles.table_rows} dataField='manage' dataFormat={linkFormatter}/>
                        </BootstrapTable>
                    </div>
                </InformationCard>
            </div>
        </div>
    );
    return returnElement;
};

export default UserProfile;
