import { CustomLabel, LabelColors, LabelTypes } from "../label";
import React from "react";
import ReactStars from "react-rating-stars-component";
import { Card } from "react-bootstrap";
import './index.css';

interface IProps {
    reviews: IReviewProps[];
}

interface IReviewProps{
    reviewedDate:string,
    user:string,
    reviewText:string,
    starRating:number
}

const ReviewCard: React.FunctionComponent<IProps> = ({
    reviews
}) => {
    const reviewsRenderArray = [];
    for (var i = 0; i < reviews.length; i++) {
        let reviewedDate = new Date(reviews[i].reviewedDate);
        reviewsRenderArray.push(
            <div key={i.toString()}>
                <Card className="shadow p-3 bg-white rounded card-wrapper review-card">
                    <Card.Header className="review-header">
                        <div className="d-flex">
                            <div className="w-25">
                                <CustomLabel
                                    children={reviews[i].user}
                                    type={LabelTypes.H4}
                                    color={LabelColors.DARKGREY}
                                    bold={true}
                                />
                            </div>
                            <div className="w-25">
                                <CustomLabel
                                    children={reviewedDate.getDate().toString() + "-" + reviewedDate.getMonth().toString() + "-" + reviewedDate.getFullYear().toString()}
                                    type={LabelTypes.H5}
                                    color={LabelColors.DARKGREY}
                                    italic={true}
                                />
                            </div>
                        </div>
                    </Card.Header>
                    <Card.Body className="review-body">
                        <CustomLabel
                            children={reviews[i].reviewText}
                            type={LabelTypes.H5}
                            color={LabelColors.DARKGREY}
                        />
                        <ReactStars
                            count={5}
                            size={24}
                            value={reviews[i].starRating}
                            isHalf={false}
                            edit={false}
                        />
                    </Card.Body>
                </Card>
            </div>
        );
    }

    return (
        <div>
            {reviewsRenderArray}
        </div>
    )
}

export default ReviewCard;
export type { IReviewProps };
