import { Auth } from "aws-amplify";
import axios from "axios";
// import { TIME_OUT, BASE_URL } from '../react-app-env';
import Logger from "./logger.service";
import { default as authService } from "./auth.service";

const http = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,
  timeout: process.env.REACT_APP_TIME_OUT
    ? parseInt(process.env.REACT_APP_TIME_OUT)
    : 10000,
});

const sessionCheck = () => {
  authService.getUser().catch((err: any) => {
    Logger.error("getCurrentAuthenticatedUser", err);
    window.location.reload();
  });
};

http.interceptors.request.use((config) => {
  if (config.params["should_autherize"]) {
    return Auth.currentSession()
      .then((session: any) => {
        if (!session) {
          sessionCheck();
          return Promise.reject(config);
        }
        config.headers.Authorization = session.idToken.jwtToken;

        return Promise.resolve(config);
      })
      .catch((error: any) => {
        Logger.error("http request", error);
        sessionCheck();
        return Promise.reject(error);
      });
  }
  return Promise.resolve(config);
});

http.interceptors.response.use(
  (response) => {
    Logger.info(`http response ${response.config.url}`, response);
    return response;
  },
  (error) => {
    Logger.error(`http response ${error.config.url}`, error);
    return Promise.reject(error.response);
  }
);

export default http;
