import { Label} from 'reactstrap';
import CustomBadge from "./custom-badge";

import './index.css';
import React, { useState, useRef } from 'react';

interface ILinkStateProps {
    itemList: IDropDownItem[],
    title: string,
    onItemSelect?: any,
    placeholder: string
}

interface IDropDownItem {
    id: number,
    name: string
}

type Props = ILinkStateProps;

const SearchableDropdown: React.FC<Props> = ({ title, itemList, placeholder }) => {

    const [keyword, setKeyword] = useState("");
    const [isFocus, setFocus] = useState(false);
    const [filterItems, setFilterItems] = useState<IDropDownItem[]>(itemList);
    const [selectedItems, setSelectedItems] = useState<IDropDownItem[]>([]);

    const multiSelectDropdown = useRef<HTMLDivElement>(null);
    
    const searchItem = (keyword: string) => {
        let result = itemList.filter(item => item.name.toLowerCase().indexOf(keyword.toLowerCase()) >= 0);
        if(result.length === 0){
            result = [{ id : -1, name : "No results found"}];
        }
        setFilterItems(result);
    }

    const handleChange = (event: any) => {
        setKeyword(event.target.value);
        searchItem(event.target.value);
    }

    const onSelect = (item: IDropDownItem) => {
        if (selectedItems.find(e => item.id === e.id) === undefined) {
            const updatedList = [...selectedItems, item];
            setSelectedItems(updatedList);
        }
    }

    const clearText = ()=> {
        setKeyword("");
        setFilterItems(itemList);
    }

    const clearSelectKeywords = ()=>{
        setSelectedItems([]);
    }

    const removeSelectedItem = (item: IDropDownItem)=>{
        const updatedlist = selectedItems.filter(e => e.id !== item.id);
        setSelectedItems(updatedlist);
    }

    const handleOnBlur = (event: any)=>{
        if(multiSelectDropdown.current != null && !(multiSelectDropdown as any).current?.contains(event.target)){
            setFocus(false);
        }
    }

    const initializeListeners = (handler: any)=>{
        document.addEventListener("mousedown", handler);
        document.addEventListener("touchstart", handler);
    }

    initializeListeners(handleOnBlur);

    return (
        <div className="container col-sm-4" onBlur={(event)=> {handleOnBlur(event)}} ref={multiSelectDropdown}>
            <Label><b>{title}</b></Label>
            <div className="selected-items-container box-shadow">
                {
                    selectedItems.map(item => {
                        return <CustomBadge item={item} action={removeSelectedItem}/>
                    })
                }
                <div className="span-clear">
                    <div
                        className={`${selectedItems.length >0 ? "span-clear-text" : "span-clear-text-disable"}`}
                        onClick={clearSelectKeywords}>clear</div>
                </div>
            </div>

            <div className="box-shadow input-box-container">
                <input type="text" className="input-box"
                       value={keyword} onChange={handleChange}
                       onFocus={()=>setFocus(true)}
                       placeholder={placeholder}
                />
                <span className="input-close-span" aria-hidden="true" onClick={clearText}>&times;</span>
            </div>

            <div tabIndex={-1} className={`${ isFocus ? "box-shadow dropdown-content" : "dropdown-content-hidden"}`} onBlur={(event)=>handleOnBlur(event)}>
                {
                    filterItems.map(item => {
                        if(item.id === -1){
                            return <label className="dropdown-item-label-disable">{item.name}</label>
                        }
                        return (<React.Fragment>
                                    <label className="dropdown-item-label" onClick={() => onSelect(item)}>
                                        {item.name}
                                        <span 
                                            className={
                                                selectedItems.filter(e => e.id === item.id).length !== 0 ? "input-tick-span" : "input-tick-span-hide"} aria-hidden="true" onClick={clearText}>&#10003;</span>
                                    </label>
                                </React.Fragment>);
                    })
                }
            </div>
        </div>
    )
};
export default SearchableDropdown;
