import { Label } from "reactstrap";

interface ILinkStateProps {
  children: string;
  type?: LabelTypes;
  color?: LabelColors;
  italic?: boolean;
  bold?: boolean;
  underlined?: boolean;
  deleted?: boolean;
  classNames?: string;
  truncateLength?: number;
}

enum LabelColors {
  PRIMARY = "primary-color",
  SECONDARY = "secondary-color",
  TERTIARY = "tertiary-color",
  DARKGREY = "darkgrey-color",
  LIGHTGREY = "lightgrey-color",
  GOLD = "gold-color",
  DARK_BROWN = "dark-brown-color",
}

enum LabelTypes {
  PRIMARY = "primary",
  SECONDARY = "secondary",
  TERTIARY = "tertiary",
  DELETED = "deleted",
  H1 = "h1-label",
  H2 = "h2-label",
  H3 = "h3-label",
  H4 = "h4-label",
  H5 = "h5-label",
  H6 = "h6-label",
}

type Props = ILinkStateProps;

const CustomLabel: React.FC<Props> = ({
  type,
  classNames,
  color,
  italic,
  bold,
  underlined,
  deleted,
  children,
  truncateLength,
}) => {
  // classNames need to be given as seperated by space (ex:- "class1 class 2 class3")
  var classes = "";

  if (type) {
    classes = classes.concat(type, " ");
  }

  if (color) {
    classes = classes.concat(color, " ");
  }

  if (classNames) {
    classes = classes.concat(classNames, " ");
  }

  if (italic) {
    classes = classes.concat("italic-label", " ");
  }

  if (bold) {
    classes = classes.concat("bold-label", " ");
  }

  if (underlined) {
    classes = classes.concat("underlined", " ");
  }

  if (deleted) {
    classes = classes.concat("delete-label", " ");
  }

  var isTruncated = false;
  var dataTip = children;

  if (truncateLength && truncateLength < children.length) {
    isTruncated = true;
    children = children.slice(0, truncateLength) + "...";
  }

  return (
    <>
      <Label
        className={` ${
          type || classNames
            ? classes.concat("default-label")
            : "default-label-style "
        }`}
        data-tip={dataTip}
        data-tip-disable={!isTruncated}
      >
        {children}
      </Label>
    </>
  );
};

export { CustomLabel, LabelTypes, LabelColors };
