import * as React from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import {
  ButtonType,
  CustomLabel,
  CustomButton,
  showToast,
  TOASTTYPE,
  LabelTypes,
  LabelColors,
} from "../../../atoms";
import { Row, Col } from "reactstrap";

interface InformationValue {
  item: string;
  itemDesc: string;
}

interface IProps {
  description: string;
  value: InformationValue[];
  isCopyAvailable: boolean;
}

interface IPriceProps {
  upperData: InformationValue[];
  lowerData: InformationValue[];
}

const InformationRow: React.FC<IProps> = ({
  description,
  value,
  isCopyAvailable,
}) => {
  return (
    <Row>
      <Col xs={4}>
        <CustomLabel
          children={description}
          type={LabelTypes.H5}
          color={LabelColors.DARKGREY}
        />
      </Col>
      <Col xs={8}>
        {value.map((valueData, key) => {
          return (
              <div key={key}>
                <>
                  {isCopyAvailable ? (
                      <>
                        <Row className="justify-content-start">
                          <div>
                            <CustomLabel
                                children={valueData.item}
                                type={LabelTypes.H5}
                                color={LabelColors.DARKGREY}
                            />
                          </div>
                          <div>
                            <CopyToClipboard
                                text={valueData.item}
                                onCopy={() => showToast("Copied !", TOASTTYPE.info)}
                            >
                              <CustomButton
                                  type={ButtonType.LINK}
                                  size={"sm"}
                                  className="copy-btn"
                                  title={"COPY"}
                              />
                            </CopyToClipboard>
                          </div>
                        </Row>
                        {valueData.itemDesc !== "" ? (
                            <Row>
                              <CustomLabel
                                  children={valueData.itemDesc}
                                  type={LabelTypes.H6}
                                  color={LabelColors.LIGHTGREY}
                              />
                            </Row>
                        ) : (
                            ""
                        )}
                      </>
                  ) : (
                      <>
                        <Row>
                          <CustomLabel
                              children={valueData.item}
                              type={LabelTypes.H5}
                              color={LabelColors.DARKGREY}
                          />
                        </Row>
                        {valueData.itemDesc !== "" ? (
                            <Row>
                              <CustomLabel
                                  children={valueData.itemDesc}
                                  type={LabelTypes.H6}
                                  color={LabelColors.LIGHTGREY}
                              />
                            </Row>
                        ) : (
                            ""
                        )}
                      </>
                  )}
                </>
              </div>
          );
        })}
      </Col>
    </Row>
  );
};

const PriceCard: React.FC<IPriceProps> = ({ upperData, lowerData }) => {
  return (
    <>
      {upperData.map((jsonData, key) => {
        return (
          <Row key={key}>
            <Col xs={4}>
              <CustomLabel
                children={jsonData.item}
                type={LabelTypes.H5}
                color={LabelColors.DARKGREY}
                bold={true}
              />
            </Col>
            <Col xs={8}>
              <CustomLabel
                children={jsonData.itemDesc}
                type={LabelTypes.H5}
                color={LabelColors.DARKGREY}
                bold={true}
              />
            </Col>
          </Row>
        );
      })}
      <hr className="divider-line" />
      {lowerData.map((jsonData, key) => {
        return (
          <Row key={key}>
            <Col xs={4}>
              <CustomLabel
                children={jsonData.item}
                type={LabelTypes.H5}
                color={LabelColors.DARKGREY}
                bold={true}
                classNames="vertical-align-sub"
              />
            </Col>
            <Col xs={8}>
              <CustomLabel
                children={jsonData.itemDesc}
                type={LabelTypes.H4}
                color={LabelColors.DARKGREY}
                bold={true}
              />
            </Col>
          </Row>
        );
      })}
    </>
  );
};

export { InformationRow, PriceCard };
export type { InformationValue };
