import * as React from 'react';

import CustomRegisterForm from '../auth-pages/register';
import CustomLoginForm from '../auth-pages/login';
import CustomOTPForm from '../auth-pages/otp';
import CustomModal from '../../molecules/modal';
import CustomSuccessRegistration from '../auth-pages/success-register';
import ForgetPasswordForm from '../auth-pages/forget-password';

enum AuthFormType {
    LOGIN = "login",
    REGISTER = "register",
    OTP = "otp",
    REGISTER_SUCCESS = "successfull-registration",
    FORGET_PASWORD = "forget-passoword"
}

interface AuthModalProps {
    showModalBool: boolean;
    setUserShowName: Function;
    closeModalAction: Function;
    setIsLoggedIn: Function;
}

const CustomAuthModal: React.FunctionComponent<AuthModalProps> = ({ showModalBool, closeModalAction, setUserShowName, setIsLoggedIn }): JSX.Element => {

    const [formTypeState, setAuthFormTypeState] = React.useState(AuthFormType.LOGIN);
    const [phoneNumberState, setPhoneNumberState] = React.useState(undefined);
    const [passwordState, setPasswordState] = React.useState(undefined);

    const changeFormType = (authFormType: AuthFormType) => {
        setAuthFormTypeState(authFormType);
    }

    const closeModal = () => {
        closeModalAction();
        // TODO - login form can be seen short time when modal is closed
        changeFormType(AuthFormType.LOGIN);
    }

    if (formTypeState === AuthFormType.REGISTER) {
        return <CustomModal closeModal={() => { closeModal() }} showModal={showModalBool}
                            content={<CustomRegisterForm
                                changeModalType={changeFormType}
                                passwordState={passwordState}
                                setPasswordState={setPasswordState}
                                phoneNumberState={phoneNumberState}
                                setPhoneNumberState={setPhoneNumberState}
                            />}
        />;
    } else if (formTypeState === AuthFormType.OTP) {
        return <CustomModal closeModal={() => { closeModal() }} showModal={showModalBool}
                            content={<CustomOTPForm
                                changeModalType={changeFormType}
                                setIsLoggedIn={setIsLoggedIn}
                                setUserShowName={setUserShowName}
                                phoneNumber={phoneNumberState}
                                password={passwordState}

                            />}
        />;
    } else if (formTypeState === AuthFormType.REGISTER_SUCCESS) {
        return <CustomModal closeModal={() => { closeModal() }} showModal={showModalBool}
                            content={<CustomSuccessRegistration
                                changeModalType={changeFormType}
                                closeModalAction={closeModalAction}
                            />}
        />;
    } else if (formTypeState === AuthFormType.FORGET_PASWORD) {
        return <CustomModal closeModal={() => { closeModal() }} showModal={showModalBool}
                            content={<ForgetPasswordForm
                                changeModalType={changeFormType}
                                closeModalAction={closeModalAction}
                            />}
        />;
    } else {
        return <CustomModal closeModal={() => { closeModal() }} showModal={showModalBool}
                            content={<CustomLoginForm
                                changeModalType={changeFormType}
                                closeModalAction={closeModalAction}
                                passwordState={passwordState}
                                setPasswordState={setPasswordState}
                                phoneNumberState={phoneNumberState}
                                setPhoneNumberState={setPhoneNumberState}
                                setIsLoggedIn={setIsLoggedIn}
                                setUserShowName={setUserShowName}
                            />}
        />;
    }

}

export { AuthFormType, CustomAuthModal };