import { Col, CustomInput } from "reactstrap";
import { CustomLabel, LabelTypes, LabelColors } from "../../../atoms";

interface ILinkStateProps {
  id: string;
  methodName: string;
  logo: string;
  disabled?: boolean;
}

type Props = ILinkStateProps;

const PaymentMethod: React.FC<Props> = ({ id, methodName, logo, disabled }) => {
  return (
    <Col xs={6} sm={4} md={3} className="p-0">
      <CustomInput
        type="radio"
        id={id}
        name="paymentMethodRadio"
        label={
          <CustomLabel
            children={methodName}
            type={LabelTypes.H5}
            color={LabelColors.DARKGREY}
          />
        }
        disabled={disabled}
      />
      <img src={logo} alt={methodName + " Icon"} height={50} width={50} />
    </Col>
  );
};

export default PaymentMethod;
