import React from "react";
import { Input } from "reactstrap";
import styles from "./index.module.css";

interface ILinkStateProps {
  id?: string;
  name?: string;
  itemList: IDropDownItem[];
  defaultItem?: number;
  onItemSelect?: any;
  className?: string;
  required?: boolean;
}

interface IDropDownItem {
  id: number;
  name: string;
  disabled: boolean;
}

type Props = ILinkStateProps;

const CustomDropdown: React.FC<Props> = ({
  id,
  name,
  itemList,
  defaultItem,
  onItemSelect,
  className,
  required,
}) => {
  const onSelectItem = (name: string, func: any) => {
    func(name);
  };

  return (
    <Input
      id={id ? id : ""}
      name={name ? name : ""}
      type="select"
      className={`${styles.dropdown_toggle} ${className ? className : ""}`}
      defaultValue={defaultItem}
      required={required ? required : true}
    >
      {itemList.map((item) => {
        return (
          <option
            value={item.id}
            onClick={() => onSelectItem(item.name, onItemSelect)}
            disabled={item.disabled}
            className={styles.opt}
            key={item.id}
          >
            {item.name}
          </option>
        );
      })}
    </Input>
  );
};

export { CustomDropdown };
