import "./index.css";
import { Modal } from "react-bootstrap";




interface IProps {
  content?: any
  title?: any
  showModal?: boolean
  closeModal?: () => void
}
const CustomModal: React.FunctionComponent<IProps> = ({
  content,
  title,
  showModal,
  closeModal

}) => {
  return (
    <>
      <Modal show={showModal} onHide={closeModal} >
        <div>
          {
            title != null && <div >
              <Modal.Header className="d-flex pt-0 pb-0 " >
                <div className="justify-content-center modal-header">{title}</div>
                <button type="button" className="closeBtn ml-auto p-0 " >
                  <span className="text-span" onClick={closeModal}>&times;</span>
                </button>
              </Modal.Header>
            </div>
          }{title == null && <div className=" rm-header d-flex " >
            <button type="button" className="closeBtn  ml-auto p-0 " >
              <span className="text-span" onClick={closeModal}>&times;</span>
            </button>
          </div>
          }
        </div>
        <div className="d-flex justify-content-center body">
          {content}
        </div>
      </Modal>
    </>
  )
}

export default CustomModal;