import './index.css';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { CustomLabel, LabelColors, LabelTypes } from "../../atoms/label";
import React, { useState } from "react";
import { CustomTextBox } from "../../atoms/text-box";
import { CustomDropdown } from "../../atoms/dropdown";
import { ButtonType, CustomButton } from "../../atoms/button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

interface IProps {
    items: any[]
}

const Cart: React.FunctionComponent<IProps> = ({
    items
}) => {

    const [promoCode, setPromoCode] = useState("");
    const [tmpItems, setTmpItems] = useState(items);
    const off = 35;

    const incrementItem = (i: number) => {
        items[i]['buyQuant'] = items[i]['buyQuant'] + 1;
        items[i]['multipliedPrice'] = items[i]['buyQuant'] * items[i]['price'];
        setTmpItems(items);
        const total = calTotal();
        setTotalPrice(total);
        setDiscountedPrice(calDiscountedPrice(total));
    };

    const decreaseItem = (i: number) => {
        if (items[i]['buyQuant'] > 1) {
            items[i]['buyQuant'] = items[i]['buyQuant'] - 1;
            items[i]['multipliedPrice'] = items[i]['buyQuant'] * items[i]['price'];
            setTmpItems(items);
            const total = calTotal();
            setTotalPrice(total);
            setDiscountedPrice(calDiscountedPrice(total));
        }
    };

    const checkout = (i: any) => {
        console.log(i);
    };

    const calDiscountedPrice = (total: number) => {
        const discount = total * off * 0.01;
        return (total - discount);
    };

    const calTotal = () => {
        let total = 0;
        tmpItems.map(i => (
            total += i['multipliedPrice']
        ));
        return total;
    };

    const [totalPrice, setTotalPrice] = useState(calTotal());
    const [discountedPrice, setDiscountedPrice] = useState(calDiscountedPrice(totalPrice));

    const deleteItem = (i: number) => {
        items.splice(i, 1);
        setTmpItems(items);
        const total = calTotal();
        setTotalPrice(total);
        setDiscountedPrice(calDiscountedPrice(total));
    };

    return (
        <div>
            {tmpItems.map(item => (
                <div key={item.id.toString()}>
                    <div className="row">
                        <div className="cols">
                            <img src={item.imgSrc.sampleCake} alt="sample-cake" height={130} width={130} />
                            <ul className='limheight'>
                                <li className='limheight-list'>
                                    <div className='item-name'>
                                        <CustomLabel
                                            children={item.name}
                                            type={LabelTypes.H6}
                                            color={LabelColors.DARKGREY}
                                        />
                                    </div>
                                </li>
                                <li className='limheight-list'>
                                    <CustomLabel
                                        children={"Rs: " + item.price}
                                        type={LabelTypes.H4}
                                        color={LabelColors.DARKGREY}
                                    />
                                </li>
                                <li className='limheight-list'>
                                    <CustomLabel
                                        children={item.unitQuantity}
                                        type={LabelTypes.H6}
                                        color={LabelColors.DARKGREY}
                                    />
                                </li>
                                <li className='limheight-list'>
                                    <CustomLabel
                                        children={item.flavour + " | " + item.itemCategory}
                                        type={LabelTypes.H6}
                                        color={LabelColors.DARKGREY}
                                    />
                                </li>
                            </ul>
                        </div>
                        <div className="cole">
                            <ul>
                                <li className='right-list'>
                                    <div className="note">
                                        <CustomLabel
                                            children={"Note"}
                                            type={LabelTypes.H6}
                                            color={LabelColors.DARKGREY}
                                        />
                                        <CustomDropdown itemList={item.additionalItems} defaultItem={1} onItemSelect={(name: string) => console.log(name)} />
                                    </div>
                                </li>
                                <li className='right-list'>
                                    <div className={'right-content'}>
                                        <CustomButton className={'increment-button-text'} onClick={() => decreaseItem(tmpItems.indexOf(item))} title="-" type={ButtonType.PRIMARY} />
                                        <CustomLabel
                                            children={item.buyQuant.toString()}
                                            type={LabelTypes.H3}
                                            color={LabelColors.DARKGREY}
                                            classNames={"calc"}
                                        />
                                        <CustomButton className={'increment-button-text'} onClick={() => incrementItem(tmpItems.indexOf(item))} title="+" type={ButtonType.PRIMARY} />
                                        <CustomLabel
                                            children={"Rs: " + item.multipliedPrice.toString()}
                                            type={LabelTypes.H3}
                                            color={LabelColors.DARKGREY}
                                            classNames={"calc"}
                                        />
                                        <a onClick={() => deleteItem(tmpItems.indexOf(item))}>
                                            <div className={'trash-border'}>
                                                <FontAwesomeIcon icon={faTrashAlt} className={'trash-icon'} />
                                            </div>
                                        </a>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="item-seperate-bar">
                        <hr />
                    </div>
                </div>
            ))}
            <div className="row">
                <div className="cols">
                    <div className="promo-code-text">
                        <CustomLabel
                            children={"Promo Code"}
                            type={LabelTypes.H5}
                            color={LabelColors.DARKGREY}
                        />
                    </div>
                    <div className="promo-code-box">
                        <CustomTextBox classNameTextBox="promo-text-box" placeHolder="Promo Code" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { setPromoCode(e.target.value) }} />
                    </div>
                    <div className="off-label">
                        <CustomLabel
                            children={off + "% OFF"}
                            type={LabelTypes.H4}
                            color={LabelColors.DARKGREY}
                            classNames={"off-value"}
                        />
                    </div>
                </div>
                <div className="cole">
                    <ul>
                        <li className='right-list'>
                            <div>
                                <CustomLabel
                                    children={"Rs: " + totalPrice.toString()}
                                    type={LabelTypes.H5}
                                    color={LabelColors.DARKGREY}
                                    classNames={"deleted"}
                                />
                                <CustomLabel
                                    children={"Total- Rs: " + discountedPrice.toString()}
                                    type={LabelTypes.H3}
                                    color={LabelColors.DARKGREY}
                                />
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <CustomButton className={'checkout-button'} onClick={checkout} title="Checkout" type={ButtonType.PRIMARY} />
        </div>
    )
};

export default Cart;
