import {Badge} from 'reactstrap';

import './index.css';

interface ILinkStateProps {
    item: IDropDownItem,
    action: any
}

interface IDropDownItem {
    id: number,
    name: string
}

type Props = ILinkStateProps;

const CustomBadge: React.FC<Props> = ({ item, action }) =>{
    return(
        <Badge className="badge-item" >{item.name}<span className="close-span" aria-hidden="true" onClick={()=>action(item)}>&times;</span></Badge>
    );
}

export default CustomBadge;