import * as React from "react";
import { CustomLabel, LabelTypes, LabelColors } from "../../atoms";
import {
  InformationRow,
  InformationValue,
  PriceCard,
} from "./information-card-row";

interface InfoCardPropsJson {
  description: string;
  value: InformationValue[];
  isCopyAvailable: boolean;
}

interface InfoCardProps {
  data?: InfoCardPropsJson[];
  heading?: string & React.ReactNode;
  subHeading?: string;
  headerClass?: string & React.ReactNode;
  isPrice?: boolean;
  children?: React.ReactNode;
}

const InformationCard: React.FC<InfoCardProps> = ({
  data,
  heading,
  subHeading,
  headerClass,
  isPrice,
  children,
}) => {
  return (
    <div className="info-card p-3">
      <div className={`${headerClass ? headerClass : "info-header"}`}>
        {heading ? (
          <CustomLabel
            type={LabelTypes.H2}
            color={LabelColors.DARKGREY}
            bold={true}
            children={heading}
          />
        ) : (
          ""
        )}
        {subHeading ? (
          <CustomLabel
            type={LabelTypes.H4}
            color={LabelColors.GOLD}
            bold={true}
            children={subHeading}
          />
        ) : (
          ""
        )}
      </div>
      {children ? children : ""}
      {data ? (
        isPrice ? (
          <PriceCard upperData={data[0].value} lowerData={data[1].value} />
        ) : (
          data.map((jsonData, key) => {
            return (
              <InformationRow
                description={jsonData.description}
                value={jsonData.value}
                isCopyAvailable={jsonData.isCopyAvailable}
                key={key}
              />
            );
          })
        )
      ) : (
        ""
      )}
    </div>
  );
};

export { InformationCard };
export type { InfoCardProps };
