import { HomePage, ShopPage, SearchPage, PaymentConfirmPage, PaymentPage, CheckoutPage } from "./pages";
import { Item } from "./components/organisms/item/index";
import { DemoPage } from "./components/organisms";
import { PrivateRoute } from "./privateRoute";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import {
  CartModal,
  UserProfile,
  OrderTrack,
} from "./components/organisms";
import PageContainer from "./components/templates/page-container";
import {ShopProfile} from "./components/organisms";


function RouteComp() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={HomePage} />
        <PrivateRoute
          exact
          path="/shop"
          component={() => <PageContainer browserBody={ShopPage} />}
        />
        <PrivateRoute exact path="/user/cart" component={CartModal} />
        <PrivateRoute exact path='/shop/item/:itemKey' component={Item} />
        <PrivateRoute
          exact
          path="/user/profile"
          component={() => <PageContainer browserBody={UserProfile} />}
        />
        <PrivateRoute exact path="/demo" component={DemoPage} />
        <PrivateRoute
          exact
          path="/search"
          component={() => <PageContainer browserBody={SearchPage} />}
        />
        <PrivateRoute exact path="/shop/ordertrack" component={OrderTrack} />
        <PrivateRoute
          exact
          path="/checkout"
          component={() => <PageContainer browserBody={CheckoutPage} />}
        />
        <PrivateRoute
          exact
          path="/payment"
          component={() => <PageContainer browserBody={PaymentPage} />}
        />
        <PrivateRoute
          exact
          path="/payment-confirm"
          component={() => <PageContainer browserBody={PaymentConfirmPage} />}
        />
      </Switch>
    </Router>
  );
}

export default RouteComp;
