import React from "react";
import {BrowserView, MobileView} from "react-device-detect";
import {Header, CommonFooter} from "../../organisms/index"
import ReactTooltip from "react-tooltip";
import styles from './index.module.css';
interface IProps {
    browserBody: any;
    mobileBody?: string;
}

const PageContainer: React.FC<IProps> = ({browserBody, mobileBody}) => {
    let isMobileAvailable = false;
    if (mobileBody) {
        isMobileAvailable = true;
    }

    function renderBody(body: any) {
        return (
            React.createElement(body)
        )
    }


    return (
        <div>
            <ReactTooltip effect="solid" type="dark" place="top" />
            <BrowserView>
                <header>
                    <Header/>
                </header>
                <div className={styles.container}>
                    {renderBody(browserBody)}
                </div>
                <footer>
                    <CommonFooter/>
                </footer>
            </BrowserView>
            <MobileView>
                <header>
                    <Header/>
                </header>
                <div className={styles.container}>
                {isMobileAvailable ? (
                    renderBody(mobileBody)
                ) : renderBody(browserBody)}
                </div>
                <footer>
                    <CommonFooter/>
                </footer>
            </MobileView>
        </div>
    );
};

export default PageContainer;