import React, { ReactElement, useEffect, useState } from "react";
import { ItemCard, ImageGrid } from "../";
import { useParams } from "react-router-dom";
import {
  Loader,
  showToast,
  TOASTTYPE,
  CustomBreadcrumb,
  CustomLabel,
  LabelTypes,
  LabelColors,
  CustomButton,
  ButtonType,
  StarRatingsWithBars,
  RatingsSummary,
  IReviewProps,
  ReviewCard,
} from "../../atoms";

import "./index.css";
import itemService from "../../../service/item.service";

interface IProps {
  itemCount: number;
}

interface IRatingProps {
  value: number;
  ratingsCount: number;
  percentage: number;
}

interface IItemPageProps {
  itemName: string;
  shopName: string;
  deletedPrice: number;
  finalPrice: number;
  soldCount: number;
  imageList: string[];
  productDetails: any[];
  note?: string;
  deliveryInfo?: string;
  ratings: IRatingProps[];
  summaryRate: number;
  totalRateCount: number;
  reviews: IReviewProps[];
  itemCards: ReactElement[];
}

const Item: React.FunctionComponent<IProps> = () => {
  const [itemCount, setItemCount] = useState(5);
  const [itemDetails, setItemDetails] = useState<IItemPageProps>();
  const params: any = useParams();
  const [loading, setLoading] = useState(false);

  const getRatingDataFormatted = (ratingData: any[]) => {
    let totalReviewedCount = 0;
    let totalRate = 0;
    let totalRatingCount = 0;

    ratingData.forEach((element) => {
      totalReviewedCount += parseInt(element["count"]);
    });
    const ratings: IRatingProps[] = ratingData.map((ratingOb) => {
      let value = parseInt(ratingOb.rate);
      let ratingCount = parseInt(ratingOb.count);
      totalRate += value * ratingCount;
      totalRatingCount += ratingCount;
      let percentage =
        (parseInt(ratingCount.toFixed(2)) / totalReviewedCount) * 100;

      return {
        value: value,
        ratingsCount: ratingCount,
        percentage: percentage,
      };
    });

    const summaryRate = (totalRate / totalRatingCount).toFixed(1);

    return { ratings, summaryRate, totalRatingCount };
  };

  useEffect(() => {
    const fetchItems = async () => {
      setLoading(true);
      const itemKey = params?.itemKey;
      try {
        const itemDetailsData = await itemService.getItemDetails(itemKey);
        if (itemDetailsData) {
          const productPropertyDetails = itemDetailsData?.productDetails?.map(
            (itemDetail: object) => ({
              description: Object.keys(itemDetail)[0],
              value: Object.values(itemDetail)[0],
            })
          );
          const ratingsOb: any = getRatingDataFormatted(
            itemDetailsData.ratings
          );
          const reviewsData: IReviewProps[] = await itemService.getItemReviews(
            itemKey,
            1,
            3
          );
          const itemCardData = await itemService.getItemsList("latest", 10);

          var itemCards: ReactElement[] = [];
          itemCardData
            ? itemCardData.forEach((item, itemCardData) => {
                itemCards.push(
                  <ItemCard key={itemCardData} {...item}></ItemCard>
                );
              })
            : itemCards.push();

          const itemDetailsOb: IItemPageProps = {
            itemName: itemDetailsData.itemName,
            shopName: itemDetailsData.shopName,
            deletedPrice: 1800,
            finalPrice: 1500,
            soldCount: itemDetailsData.soldCount,
            imageList: itemDetailsData.imageList,
            productDetails: productPropertyDetails,
            note: itemDetailsData.note,
            deliveryInfo: itemDetailsData.deliveryInfo,
            ratings: ratingsOb["ratings"],
            totalRateCount: ratingsOb["totalRatingCount"],
            summaryRate: ratingsOb["summaryRate"],
            reviews: reviewsData,
            itemCards: itemCards,
          };
          setItemDetails(itemDetailsOb);
          setLoading(false);
        }
      } catch (error) {
        setLoading(false);
        showToast("Page Loading Error", TOASTTYPE.error);
      }
    };
    fetchItems();
  }, []);

  const productDetailsRenderArray = [];
  if (itemDetails?.productDetails) {
    for (var i = 0; i < itemDetails?.productDetails?.length; i++) {
      productDetailsRenderArray.push(
        <div className="d-flex" key={i.toString()}>
          <div className="product-details-column">
            <CustomLabel
              children={itemDetails?.productDetails[i].description}
              type={LabelTypes.H5}
              color={LabelColors.DARKGREY}
            />
          </div>
          <div className="product-details-column">
            <CustomLabel
              children={itemDetails?.productDetails[i].value}
              type={LabelTypes.H5}
              color={LabelColors.DARKGREY}
            />
          </div>
        </div>
      );
    }
  }

  if (loading) {
    return (
      <div>
        <Loader isLoading={true} />
      </div>
    );
  }
  return (
    <div>
      <div>
        <CustomBreadcrumb
          pathList={[
            {
              text: "Home",
              link: "/",
            },
            {
              text: "Shops",
              link: "/shop",
            },
            {
              text: "Nish Cake",
              link: "/shop/NishCake",
            },
            {
              text: "Chocolate Cake Special",
              link: "",
            },
          ]}
        ></CustomBreadcrumb>
      </div>
      <div className="d-flex">
        <div className="w-50">
          {itemDetails?.imageList ? (
            <ImageGrid imageList={itemDetails?.imageList} />
          ) : null}
        </div>
        <div className="item-header w-50">
          <div className="item-name">
            {itemDetails?.itemName ? (
              <CustomLabel
                children={itemDetails.itemName}
                type={LabelTypes.H2}
                color={LabelColors.DARKGREY}
                bold
              />
            ) : null}
          </div>
          <div className="item-owner">
            {itemDetails?.shopName ? (
              <CustomLabel
                children={itemDetails?.shopName}
                type={LabelTypes.H2}
                color={LabelColors.DARKGREY}
              />
            ) : null}
          </div>
          <div className="item-price">
            <div className="item-price-dropped">
              {itemDetails?.deletedPrice ? (
                <CustomLabel
                  children={"Rs.".concat(
                    (itemDetails?.deletedPrice).toString()
                  )}
                  type={LabelTypes.H3}
                  color={LabelColors.LIGHTGREY}
                  deleted
                />
              ) : null}
            </div>
            {itemDetails?.finalPrice ? (
              <CustomLabel
                children={"Rs.".concat(itemDetails?.finalPrice.toString())}
                type={LabelTypes.H1}
                color={LabelColors.DARKGREY}
              />
            ) : null}
          </div>
          <div className="item-sold">
            <span>
              <b>{itemDetails?.soldCount ? itemDetails.soldCount : 0}</b> items
              sold
            </span>
          </div>

          <div className="d-flex item-header-button">
            <div className="d-flex">
              <div>
                <CustomButton
                  title="-"
                  type={ButtonType.SECONDARY}
                  onClick={() => {
                    setItemCount(Math.max(itemCount - 1, 0));
                  }}
                />
              </div>
              <div className="item-count">
                <CustomLabel
                  children={itemCount.toString()}
                  type={LabelTypes.H4}
                  color={LabelColors.DARKGREY}
                />
              </div>
              <div>
                <CustomButton
                  title="+"
                  type={ButtonType.PRIMARY}
                  onClick={() => {
                    setItemCount(itemCount + 1);
                  }}
                />
              </div>
            </div>
            <div className="item-button">
              <CustomButton
                title={"Add to cart"}
                type={ButtonType.SECONDARY}
              ></CustomButton>
            </div>
            <div className="item-button">
              <CustomButton
                title={"Buy Now"}
                type={ButtonType.PRIMARY}
              ></CustomButton>
            </div>
          </div>
        </div>
      </div>

      <div>
        <div className="item-page-main-header">
          <CustomLabel
            children={"Details"}
            type={LabelTypes.H1}
            color={LabelColors.DARKGREY}
            underlined
            bold
          />
        </div>

        <div className="d-flex">
          <div className="product-details">
            <div className="item-page-sub-header">
              <CustomLabel
                children={"Product Details"}
                type={LabelTypes.H2}
                color={LabelColors.DARKGREY}
              />
            </div>
            {productDetailsRenderArray}
          </div>

          {itemDetails?.note ? (
            <div className="product-notice">
              <div className="item-page-sub-header">
                <CustomLabel
                  children={"Please Note"}
                  type={LabelTypes.H2}
                  color={LabelColors.DARKGREY}
                />
              </div>
              <CustomLabel
                children={itemDetails?.note}
                type={LabelTypes.H5}
                color={LabelColors.DARKGREY}
              />
            </div>
          ) : null}
        </div>

        {itemDetails?.deliveryInfo ? (
          <div className="product-delivery">
            <div className="item-page-sub-header">
              <CustomLabel
                children={"Delivery Information"}
                type={LabelTypes.H2}
                color={LabelColors.DARKGREY}
              />
            </div>
            <CustomLabel
              children={itemDetails?.deliveryInfo}
              type={LabelTypes.H5}
              color={LabelColors.DARKGREY}
            />
          </div>
        ) : null}
      </div>

      <div>
        <div className="item-page-main-header">
          <CustomLabel
            children={"Ratings & Reviews"}
            type={LabelTypes.H1}
            color={LabelColors.DARKGREY}
            underlined
            bold
          />
        </div>
        <div className="d-flex ratings-container">
          <div className="w-75">
            <StarRatingsWithBars
              ratings={itemDetails?.ratings}
              totalStars={5}
            ></StarRatingsWithBars>
          </div>
          <div className="w-25">
            <RatingsSummary
              ratingsSummary={{
                value: itemDetails?.summaryRate,
                totalRatings: itemDetails?.totalRateCount,
              }}
              totalStars={5}
            ></RatingsSummary>
          </div>
        </div>

        <div className="reviews">
          {itemDetails?.reviews ? (
            <ReviewCard reviews={itemDetails?.reviews}></ReviewCard>
          ) : null}
        </div>
      </div>

      <div>
        {itemDetails?.itemCards ? (
          <>
            <div className="item-page-main-header">
              <CustomLabel
                children={"Related Products"}
                type={LabelTypes.H1}
                color={LabelColors.DARKGREY}
                bold
                underlined
              />
            </div>
            <div className="d-flex">{itemDetails.itemCards}</div>
          </>
        ) : null}
      </div>
    </div>
  );
};

export { Item };
export type { IItemPageProps, IRatingProps };
