import React, { useState, useEffect } from "react";
import ReactStars from "react-rating-stars-component";
import { CustomLabel, LabelColors, LabelTypes } from "../";
import ProgressBar from 'react-bootstrap/ProgressBar';
import Util from "../../../service/util.service";
import "./index.css";

interface IProps {
  ratings?: any[];
  ratingsSummary?: any;
  totalStars: number;
}

interface IRatingProps {
  value: number;
  count?: number;
  edit?: boolean;
  isHalf?: boolean;
  onChange?:Function
}

const StarRatingsWithBars: React.FunctionComponent<IProps> = ({
  ratings,
  totalStars,
}) => {
    const ratingStars: any[] = [];
    if (ratings && ratings.length > 0) {
        for (let i = 0; i < ratings.length; i++) {
            ratingStars.push(
                <div className="d-flex" key={i.toString()}>
                    <div className="d-flex rating-stars-container">
                        <div className="rating-stars">
                            <ReactStars
                                count={totalStars}
                                size={24}
                                value={ratings[i].value}
                                isHalf={false}
                                edit={false}
                            />
                        </div>

            <div className="d-flex rating-count">
                <CustomLabel
                    children={`(` + ratings[i].ratingsCount + `)`}
                    type={LabelTypes.H5}
                    color={LabelColors.DARKGREY}
                />
            </div>
          </div>

          <div className="rating-bar">
            <ProgressBar now={ratings[i].percentage} variant="warning" />
          </div>
        </div>
      );
    }
  }
  return (
    <div>
      {ratingStars}
    </div>
  );
};

const RatingsSummary: React.FunctionComponent<IProps> = ({
  ratingsSummary,
  totalStars,
}) => {
  return (
    <div className="rating-summary">
        <CustomLabel
            children={ratingsSummary.value}
            type={LabelTypes.H1}
            color={LabelColors.DARKGREY}
            bold={true}
        />
        <div className="align-stars-center">
            <ReactStars
                count={totalStars}
                size={24}
                value={ratingsSummary.value}
                isHalf={true}
            />
        </div>
        <CustomLabel
            children={ratingsSummary.totalRatings + ` ratings`}
            type={LabelTypes.H3}
            color={LabelColors.DARKGREY}
        />
    </div>
  );
};

const RatingStar: React.FunctionComponent<IRatingProps> = ({
  value,
  count,
  edit,
  isHalf,
  onChange
}) => {
  function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(
      Util.getWindowDimensions()
    );

    useEffect(() => {
      function handleResize() {
        setWindowDimensions(Util.getWindowDimensions());
      }

      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }, []);

    return windowDimensions;
  }

  const { height, width } = useWindowDimensions();
  return (
    <ReactStars
      count={count ? count : 5}
      size={() => {
        if (width > 1200) {
          return 20;
        } else if (width > 880) {
          return 16;
        } else if (width > 550) {
          return 12;
        } else if (width > 390) {
          return 10;
        } else {
          return 8;
        }
      }}
      edit={edit ? edit : false}
      value={value}
      onChange={onChange}
      isHalf={isHalf ? isHalf : true}
      activeColor="#f2b544"
    />
  );
};

export { StarRatingsWithBars, RatingsSummary, RatingStar };
