import CheckoutProgressItem from "./checkout-progress-item";
import {
  faShoppingBasket,
  faCreditCard,
  faFileInvoice,
} from "@fortawesome/free-solid-svg-icons";

interface ILinkStateProps {
  checkoutStep: CheckoutStep;
}

enum CheckoutStep {
  CHECKOUT,
  PAYMENT,
  CONFIRMATION,
}

type Props = ILinkStateProps;

const CheckoutProgress: React.FC<Props> = ({ checkoutStep }) => {
  return (
    <div>
      <div className="d-flex align-items-center justify-content-center">
        <CheckoutProgressItem
          itemText="1. Checkout"
          active={checkoutStep === CheckoutStep.CHECKOUT}
          icon={faShoppingBasket}
        />

        <div className="w-15 p-md-3 p-sm-2 p-1">
          <hr className="progress-line" />
        </div>

        <CheckoutProgressItem
          itemText="2. Payment"
          active={checkoutStep === CheckoutStep.PAYMENT}
          icon={faCreditCard}
        />

        <div className="w-15 p-md-3 p-sm-2 p-1">
          <hr className="progress-line" />
        </div>

        <CheckoutProgressItem
          itemText="3. Confirmation"
          active={checkoutStep === CheckoutStep.CONFIRMATION}
          icon={faFileInvoice}
        />
      </div>
    </div>
  );
};

export { CheckoutProgress, CheckoutStep};
