
import { Flip, toast, TypeOptions} from 'react-toastify';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheckCircle,faExclamationCircle,faTimesCircle, IconDefinition} from '@fortawesome/free-solid-svg-icons';
import 'react-toastify/dist/ReactToastify.css';


export enum TOASTTYPE {
  info = 'info',
  error = 'error',
  success = 'success',
  warning = 'warn' 
}

export function showToast(message: String,type:TypeOptions) {
    let icon : IconDefinition;
    if(type === 'success'){
      icon = faCheckCircle;
    } else if(type === 'error'){
      icon = faTimesCircle;
    }else {
      icon = faExclamationCircle;
    }
     
    toast(
      <div className="d-flex flex-direction-row justify-content-start align-items-center">
    <FontAwesomeIcon
      className="secondary-text-color align-items-left"
      icon={icon}
      size="2x" 
    /> <div className="ml-2">{message}</div>
    </div>
      , {
      position: toast.POSITION.TOP_RIGHT,
      transition : Flip ,
      pauseOnHover: true,
      autoClose: 5000,
      hideProgressBar: true,
      type: type
    });
}
