import React, { FC, useState } from "react";
import { Card, Collapse } from "react-bootstrap";
import {
  CustomButton,
  CustomLabel,
  LabelTypes,
  LabelColors,
  ButtonType,
  RatingStar,
} from "../../atoms";
import { useHistory } from "react-router-dom";
import styles from "./index.module.css";

interface IItemCardProps {
  imagePath: string;
  title: string;
  itemKey: string;
  rating: number;
  shopName: string;
  price: number; //if discount given-> this is previous price
  discount?: number;
  isDiscountPercentage?: boolean;
  weight?: string;
  width?: string;
}

type Props = IItemCardProps;

const ItemCard: FC<Props> = ({
  imagePath,
  title,
  rating,
  shopName,
  discount,
  isDiscountPercentage,
  weight,
  price,
  width,
}) => {
  const [rate, setRate] = useState(5);
  const [addToCartShow, setAddtoCartShow] = useState(false);
  const history = useHistory();
  var newPrice: number;
  var discountStr: string = "";
  const prePrice: number = price;

  if (discount) {
    if (isDiscountPercentage === true) {
      const discountAmount: number = (prePrice / 100) * discount;
      discountStr = discount?.toString().concat("% Off");
      newPrice = prePrice - discountAmount;
    } else {
      discountStr = discount?.toString().concat(" Off");
      newPrice = prePrice - discount;
    }
  } else {
    newPrice = price;
  }

  const prePriceStr: string = "Rs.".concat(prePrice.toFixed(2).toString());
  const priceStr: string = "Rs.".concat(newPrice.toFixed(2).toString());

  return (
    <div
      className={"d-flex align-items-start " + styles.card_container}
      style={{ width: width }}
    >
      <Card
        className={"shadow p-3 mb-5 bg-white rounded " + styles.cardWrapper}
        style={{ margin: "20px" }}
        onMouseEnter={() => setAddtoCartShow(true)}
        onMouseLeave={() => setAddtoCartShow(false)}
      >
        <Card.Img variant="top" src={imagePath} />
        <Card.Body>
          <div className="d-flex flex-row align-items-center">
            <CustomLabel
              children={title}
              type={LabelTypes.H3}
              color={LabelColors.DARKGREY}
            />
            <div style={{ marginLeft: "10px" }}>
              {weight ? (
                <CustomLabel
                  children={weight}
                  type={LabelTypes.H5}
                  color={LabelColors.DARKGREY}
                />
              ) : null}
            </div>
          </div>
          <div>
            <RatingStar value={rate} />
          </div>
          <div className="d-flex flex-column">
            <CustomLabel
              children={shopName}
              type={LabelTypes.H5}
              color={LabelColors.DARKGREY}
            />
            {discountStr !== "" ? (
              <>
                <CustomLabel
                  children={discountStr}
                  type={LabelTypes.H5}
                  color={LabelColors.DARKGREY}
                />
                <CustomLabel
                  children={prePriceStr}
                  type={LabelTypes.H6}
                  color={LabelColors.DARKGREY}
                  deleted={true}
                />
              </>
            ) : null}
          </div>
          <CustomLabel
            children={priceStr}
            type={LabelTypes.H3}
            color={LabelColors.DARKGREY}
          />
          <Collapse in={addToCartShow}>
            <div
              className={
                addToCartShow ? "d-flex justify-content-center" : "d-none"
              }
            >
              <CustomButton
                title={"Add to cart"}
                type={ButtonType.PRIMARY}
              ></CustomButton>
            </div>
          </Collapse>
        </Card.Body>
      </Card>
    </div>
  );
};

export { ItemCard };
export type { IItemCardProps };
