import StoreElement from "./store-element";
import { Row, Col } from "reactstrap";

interface ILinkStateProps {
  storeList: IStoreItem[];
}

interface IStoreItem {
  storeId: string;
  storeName: string;
  imgUrl: string;
}

type Props = ILinkStateProps;

const FeaturedStore: React.FC<Props> = ({ storeList }) => {
  if(storeList.length < 5 ){
    return (
      <div></div>
    )
  }
  return (
    <Row className="m-0">
      <Col sm="8" className="p-0">
        <Row className="m-0">
          <Col xs="8" className="first">
            <div className="single shadow-border">
              <StoreElement classType="single" storeData={storeList[0]} />
            </div>
          </Col>
          <Col xs="4" className="second">
            <div className="single shadow-border">
              <StoreElement classType="single" storeData={storeList[1]} />
            </div>
          </Col>
        </Row>
        <Row className="m-0">
          <Col xs="4" className="p-0">
            <div className="single shadow-border">
              <StoreElement classType="single" storeData={storeList[2]} />
            </div>
          </Col>

          <Col xs="8" className="forth">
            <div className="single shadow-border">
              <StoreElement classType="single" storeData={storeList[3]} />
            </div>
          </Col>
        </Row>
      </Col>
      <Col sm="4" className="fifth">
        <div className="double shadow-border">
          <StoreElement classType="double" storeData={storeList[4]} />
        </div>
      </Col>
    </Row>
  );
};

export {FeaturedStore};
export type { IStoreItem };
