import { RotateLoader } from 'react-spinners';
import "./index.css";

interface IProps {
  isLoading?: boolean;
  text?: string;
  className?: string;
}

const Loader: React.FunctionComponent<IProps> = ({
  isLoading,
  text,
  className
}) => {
  return (
    <div className={className}>
      <div
        className={isLoading ? 'parent-disable' : ''}
        style={{ width: '100%' }}
      >
        <div className="overlay-box">
          <RotateLoader
            color={'#000000'}
            loading={isLoading}
            speedMultiplier={0.7}
          />
        </div>
      </div>
    </div>
  )
}

export default Loader;