import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import './index.css';

interface IProps {
    imageList: string[];
}

const ImageGrid: React.FunctionComponent<IProps> = ({
    imageList
}) => {
    const items: any[] = [];
    for (let i = 0; i < imageList.length; i++) {
        items.push(
            <div key={i.toString()}>
                <img src={imageList[i]} alt={imageList[i]} />
            </div>
        );
    }

    return (
        <div className="image-grid-container">
            <Carousel className="image-carousel w-100 h-100" showThumbs={true} useKeyboardArrows={true} infiniteLoop={true}>
                {items}
            </Carousel>
        </div>
    )
}

export default ImageGrid;
