import Breadcrumb from 'react-bootstrap/Breadcrumb';
import './index.css';

interface IProps {
    pathList?: any[];
}

const CustomBreadcrumb: React.FunctionComponent<IProps> = ({
    pathList
}) => {
    const items: any[] = [];
    if (pathList && pathList.length > 1) {
        for (let i = 0; i < pathList.length - 1; i++) {
            items.push(
                <Breadcrumb.Item key={i.toString()} href={pathList[i].link} className="item">
                    {pathList[i].text}
                </Breadcrumb.Item>
            );
        }
        items.push(
            <Breadcrumb.Item active className="active" key={(pathList.length - 1).toString()}>
                {pathList[pathList.length - 1].text}
            </Breadcrumb.Item>
        );
    }
    return (
        <div className="breadcrumb">
            <Breadcrumb>
                {items}
            </Breadcrumb>
        </div>
    )
}

export default CustomBreadcrumb;
