import * as React from 'react';
import { Container, Input } from 'reactstrap';
import { getPhoneNumberWithCountryCode, isPhoneNumberValid } from '../../../../commons/util/validation'
import { ButtonType, CustomButton, CustomLabel, LabelTypes, CustomTextBox, showToast, TOASTTYPE, LabelColors } from '../../../atoms';
import Icon from '@iconify/react';
import facebook from '@iconify-icons/logos/facebook';
import googleGmail from '@iconify-icons/logos/google-gmail';
import {AuthFormType} from '../../auth-modal';
import { AuthService, AuthServiceImpl, AuthException } from '../../../../service/auth.service';

interface LoginFormProps {
    changeModalType: Function;
    closeModalAction: Function;
    setPhoneNumberState: Function;
    setPasswordState: Function;
    setUserShowName: Function;
    setIsLoggedIn: Function;
    phoneNumberState: string|undefined;
    passwordState: string|undefined;
}

const CustomLoginForm: React.FunctionComponent<LoginFormProps> = ({ changeModalType, closeModalAction, setPhoneNumberState, setUserShowName, setIsLoggedIn, setPasswordState, phoneNumberState, passwordState }): JSX.Element => {

    const authService: AuthService = new AuthServiceImpl();

    const onPhoneNumberChange = (phoneNumber:string) => {
        setPhoneNumberState(phoneNumber);
    }

    const onPasswordChange = (password:string) => {
        setPasswordState(password);
    }

    const validateFormRealTime = () => {
        if (!(passwordState && phoneNumberState)) {
            return false;
        } else if (passwordState === "" || phoneNumberState === "") {
            return false;
        } else if (!isPhoneNumberValid(phoneNumberState)) {
            return false;
        }
        return true;
    }

    const userLoginTrigger = (e: React.ChangeEvent<HTMLButtonElement>) => {
        e.preventDefault();
        if (phoneNumberState && passwordState) {
            let phoneNumberWithCountryCode = getPhoneNumberWithCountryCode(phoneNumberState);
            authService.logIn(phoneNumberWithCountryCode, passwordState)
                .then(async ({ error }) => {
                    if (error) {
                        if (error.code === AuthException.USER_NOT_CONFIRMED) {
                            let { error } = await authService.sendOTP(phoneNumberWithCountryCode);
                            if (!error) {
                                showToast("USER NOT CONFIRMED, OTP SENT TO PHONENUMBER", TOASTTYPE.info);
                                changeModalType(AuthFormType.OTP);
                            } else {
                                showToast(error.message, TOASTTYPE.error);
                            }
                        } else {
                            console.error("LOGIN FAILED");
                            showToast(error.message, TOASTTYPE.error);
                        }
                    } else {
                        console.log("LOGIN SUCCESS");
                        setUserShowName(await authService.getUserShowName());
                        setIsLoggedIn(true);
                        closeModalAction();
                    }
                })
                .catch(async () => {
                    console.error("LOGIN FAILED");
                    showToast("Unexpected error occurred! Try again!", TOASTTYPE.error);
                });
        } else {
            console.warn("Insufficient details");
            showToast("Insufficient details to submit OTP. Try again !", TOASTTYPE.error);
        }
    }

    return (
        <div className="form">
            <Container className="form-container">
                <CustomLabel
                    children={"Login"}
                    type={LabelTypes.H5}
                    color={LabelColors.DARKGREY}
                />
                <hr />
                <br />
                <form>
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Phone number" onchange={(e: React.ChangeEvent<HTMLInputElement>) => { onPhoneNumberChange(e.target.value) }} />
                    <br />
                    <CustomTextBox classNameTextBox="form-text-box" placeHolder="Password" type={"password"} onchange={(e: React.ChangeEvent<HTMLInputElement>) => { onPasswordChange(e.target.value) }} />
                    <br />
                    <CustomButton className="form-button-submit" onClick={userLoginTrigger} title="Log In" type={ButtonType.PRIMARY} disabled={!validateFormRealTime()} />
                    <br />
                    <div className="form-remember-me">
                        <Input className="form-checkbox" type="checkbox" />
                        <div className="form-checkbox-text">
                            <p className="form-text">Remember me</p>
                        </div>
                    </div>
                    <div className="form-federated-row" >
                        <button className="form-icon-btn btn" onClick={(e) => {
                            e.preventDefault();
                            authService.facebookSignIn();
                        }}>
                            <Icon icon={facebook} height="50px" width="50px" />
                        </button>
                        <button className="form-icon-btn btn" onClick={(e) => {
                            e.preventDefault();
                            authService.googleSignIn();
                        }}>
                            <Icon icon={googleGmail} height="50px" width="50px" />
                        </button>
                    </div>
                    <div>
                        <span>
                            <p className="form-text">Don,t you have a CakeHub account ?</p> <p onClick={() => { changeModalType(AuthFormType.REGISTER) }} className="form-link-text">Register now !</p>
                        </span>
                        <br />
                        <p className="form-link-text" onClick={() => { changeModalType(AuthFormType.FORGET_PASWORD) }}>Forgot password ?</p>
                    </div>
                </form>
            </Container>
            <br />
        </div>
    );
}

export default CustomLoginForm;