import http from './http.service';
import { IItemCardProps,IStoreItem } from "../components/organisms";
import { IReviewProps } from '../components/atoms';

class ItemService {
    public async getItemsList(type:string,pageSize:number):Promise<IItemCardProps[]>{
        const url = `?pageSize=${pageSize}&type=${type}`;
        http.defaults.params = {should_autherize:false};
        const results = await http.get(url);
        var itemsList:IItemCardProps[]=[]
        results?.data?.data?.forEach((item: { weight: any; weightUnit: any;itemKey:any; imageUrl: any; name: any; rating: any; shopName: any; price: any;offerValue:any;offerUnit:any }) => {
            const weightString:string=item.weight + item.weightUnit
            const discount:number=parseFloat(item.offerValue)
            const isDiscountPercentage:boolean=(item.offerUnit==="percentage"?true:false)
            const itemCard:IItemCardProps={
                imagePath:item.imageUrl,
                title: item.name,
                itemKey:item.itemKey,
                rating: parseFloat(item.rating),
                shopName: item.shopName,
                price: parseFloat(item.price),  //if discount given-> this is previous price
                weight: weightString,
                discount:discount,
                isDiscountPercentage:isDiscountPercentage

            };
            itemsList.push(itemCard)
            
        });
        return itemsList;
    }


    public async getItemDetails(itemKey:string):Promise<any>{

        const url = `getItem/${itemKey}`;
        http.defaults.params = {should_autherize:false};
        const results = await http.get(url);
        const pageData=results.data
        const itemDetails:object = {
            itemName: pageData.itemName,
            shopName: pageData.shopName,
            price:pageData.price,
            soldCount: pageData.soldCount,
            imageList: pageData.imageList,
            productDetails:pageData.itemPropertyDetails,
            note: pageData.note,
            deliveryInfo:pageData.deliveryDescription,
            ratings: pageData.ratings

        }
        
        return itemDetails;
    }

    public async getItemReviews(itemKey:string,pageNumber:number,pageSize:number):Promise<IReviewProps[]>{

        const url = `getReviews/${itemKey}?pageSize=${pageSize}&pageNumber=${pageNumber}&type=latest`;
        http.defaults.params = {should_autherize:false};
        const results = await http.get(url);
        const reviewData=results?.data?.data
        const reviewList:IReviewProps[] = reviewData.map((review:any)=>({
            reviewedDate:review.reviewedDate,
            user:review.userName,
            reviewText:review.review,
            starRating:review.rate,
        }))
        
        return reviewList;

    }
    public async getFeaturedStores():Promise<IStoreItem[]>{
        //todo - url
        let url: string = '';
        http.defaults.params = {should_autherize:false};
        const results = await http.get(url);
        var feturedStores:IStoreItem[] = [];
        results?.data?.data?.forEach((fStore: { storeId: any; storeName: any; imgUrl: any; }) => {
            const fStoreItem:IStoreItem={
                storeId:fStore.storeId,
                storeName: fStore.storeName,
                imgUrl: fStore.imgUrl

            };
            feturedStores.push(fStoreItem)
        });
        return feturedStores;
    }
}

export default new ItemService();
