import React from "react";
import { Carousel, Image } from "react-bootstrap";
import { sampleCake } from "../../../assets";
import { CustomLabel, LabelColors, LabelTypes } from "../../atoms";
import styles from "./index.module.css";

export interface IProps {
  items: Array<CarouselItemProps>;
}

export interface CarouselItemProps {
  img?: CarouselImage;
  caption?: Caption;
  clickable?: Clickable;
  interval?: number;
  className?: string;
}

export enum ChildPositionInCarousal {
  BOTTOM_RIGHT = "BOTTOM_RIGHT",
  BOTTOM_LEFT = "BOTTOM_LEFT",
  BOTTOM_CENTER = "BOTTOM_MIDDLE",
}

const PositionMap = new Map<ChildPositionInCarousal, string>([
  [ChildPositionInCarousal.BOTTOM_LEFT, styles.carousel_bottom_left],
  [ChildPositionInCarousal.BOTTOM_RIGHT, styles.carousel_bottom_right],
  [ChildPositionInCarousal.BOTTOM_CENTER, styles.carousel_bottom_center],
]);

export interface CarouselImage {
  imgSrc: string;
  imgAlt: string;
  className: string;
}

export interface Caption {
  captionPrimary: CaptionPrimary;
  captionSecondary?: CaptionSecondary;
  captionBackground?: string;
  className?: string;
}

export interface CaptionPrimary {
  caption: string;
  className?: string;
}

export interface CaptionSecondary {
  caption: string;
  className?: string;
}

export interface Clickable {
  clickableItem: JSX.Element;
  position?: ChildPositionInCarousal;
  className?: string;
}

export const CustomCarousal: React.FunctionComponent<IProps> = ({
  items,
}: IProps): JSX.Element => {
  let carouselItems = items.map(generateCarousalItem);
  return (
    <div className={styles.carousel_container} id="carousel-container">
      <Carousel
        className={styles.carousel_indicators}
        prevIcon={
          <span
            id="carousel-prev"
            aria-hidden="false"
            className={`carousel-control-prev-icon ${styles.carousel_control_prev_icon}`}
          />
        }
        nextIcon={
          <span
            id="carousel-next"
            aria-hidden="false"
            className={`carousel-control-next-icon ${styles.carousel_control_next_icon}`}
          />
        }
        fade={true}>
        {carouselItems}
      </Carousel>
    </div>
  );
};

const generatePositionStyles = (position: ChildPositionInCarousal): string => {
  let clazz = PositionMap.get(position);
  return clazz ? clazz : styles.carousel_bottom_center;
};

const generateCarousalItem = ({
  img = { imgSrc: sampleCake, imgAlt: "Carousel image", className: "" },
  caption,
  clickable,
  interval,
  className,
}: CarouselItemProps): JSX.Element => {
  return (
    <Carousel.Item
      interval={interval}
      className={`${caption? styles.overlay:""} ${styles.carousel} ${
        className ? className : ""
      }`}
    >
      {caption?
      <Carousel.Caption
        className={`${styles.carousel_caption1} ${
          caption.className ? caption.className : ""
        }`}
      >
        <div className={"d-flex flex-column"}>
          {caption.captionPrimary?(
            <div>
              <CustomLabel type={LabelTypes.H1} bold={true} color={LabelColors.DARK_BROWN} 
              classNames={`${styles.carousel_caption1_text} 
                ${caption.captionPrimary.className? caption.captionPrimary.className: ""}`}
                children ={caption.captionPrimary.caption}
              />
            </div>
          ):("")}
          {caption.captionSecondary ? (
            <div>
              <CustomLabel
                type={LabelTypes.H4}
                bold={true}
                color={LabelColors.DARK_BROWN}
                classNames={`${styles.carousel_caption2_text} 
                             ${caption.captionSecondary.className ? caption.captionSecondary.className : "" }`}
                children={caption.captionSecondary.caption}
              />
            </div>
          ) : (
            ""
          )}
        </div>
      </Carousel.Caption>
      :""}
      {clickable ? (
        <div className={`${styles.carousel_caption2} d-flex flex-row`}>
          <Carousel.Caption
            className={` ${clickable.className ? clickable.className : ""} 
            ${
              clickable.position
                ? generatePositionStyles(clickable.position)
                : ""
            }`}
          >
            {clickable.clickableItem}
          </Carousel.Caption>
        </div>
      ) : (
        ""
      )}
      <div className={styles.carousel_img_container}>
        <Image
          src={img.imgSrc}
          alt={img.imgAlt}
          className={img.className ? img.className : ""}
        />
      </div>
    </Carousel.Item>
  );
};
