import './index.css';
import CustomModal from "../../molecules/modal";
import Cart from "../cart";
import {cart} from "../../../store/cart/selectors";
import {useSelector} from "react-redux";

const CartModal:React.FunctionComponent = (): JSX.Element =>{
    const items: any = useSelector(cart);
    let returnElement :JSX.Element = <div></div>;
    returnElement= <CustomModal showModal={true} content={<div className='content-class'><Cart items={items.items}/></div>}/>;
    return returnElement;
};

export default CartModal;
