import { Auth } from 'aws-amplify';
import { CognitoHostedUIIdentityProvider } from "@aws-amplify/auth/lib/types";
import http from "./http.service";

export interface AuthService {
    register(phoneNumber: string, password: string, name: string): Promise<any>;
    logIn(phoneNumber: string, password: string): Promise<any>;
    logOut(): Promise<any>;
    submitOTP(phoneNumber:string, code:string): Promise<any>;
    sendOTP(phoneNumber:string): Promise<any>;
    changePassword(currentPassword: string, newPassword: string): Promise<any>;
    facebookSignIn(): void;
    googleSignIn(): void;
    getUser(): any;
    getToken(): any;
    isLoggedIn(): Promise<boolean>;
    forgotPasswordRequest(username: string): Promise<void>;
    forgotPasswordResponse(username: string, code: string, newPassword: string): Promise<void>;
    getUserShowName(): Promise<string|undefined>;
}

export enum AuthException {
    INVALID_PASSWORD = "InvalidPasswordException",
    INVALID_USERNAME_OR_PASSWORD = "NotAuthorizedException",
    INVALID_PARAMETER = "InvalidParameterException",
    USERNAME_EXISTS = "UsernameExistsException",
    AMPLIFY_AUTH_ERROR = "AuthError",
    USER_NOT_CONFIRMED = "UserNotConfirmedException",
    OTP_EXPIRED = "OTP_EXPIRED",
    OTP_INVALID = "OTP_INVALID",
    USER_NOT_FOUND = "UserNotFoundException",
    UNEXPECTED_ERROR = "UNEXPECTED_ERROR"
}

export class AuthServiceImpl implements AuthService {

    // OTP is sent by pre signup trigger
    async register(phoneNumber: string, password: string, name: string): Promise<any> {
        try {
            await Auth.signUp({
                username: phoneNumber,
                password,
                attributes : {
                    "name": name
                }
            });
            return { error: undefined };
        } catch (error) {
            return { error: AuthServiceImpl.getErrorResponse(error) };
        }
    }

    async forgotPasswordResponse(username: string, code: string, newPassword: string): Promise<void> {
        // connect to backend
        return;
    }

    async forgotPasswordRequest(username: string): Promise<void> {
        // connect to backend
        return;
    }

    async isLoggedIn(): Promise<boolean> {
        try {
            await Auth.currentAuthenticatedUser({
                bypassCache: false
            });
            return true;
        } catch (error) {
            return false;
        }
    }

    async getToken() {
        const user = await Auth.currentAuthenticatedUser();
        return user.getSignInUserSession()?.getIdToken().getJwtToken();
    }

    private async onLogoutSession(): Promise<any> {
        // TODO - Clear session storages etc

    }

    async logOut(): Promise<any> {
        try {
            await Auth.signOut();
            await this.onLogoutSession();
            return { error: undefined };
        } catch (error) {
            return { error };
        }
    }

    async changePassword(currentPassword: string, newPassword: string): Promise<any> {
        const user = await Auth.currentAuthenticatedUser();
        return await Auth.changePassword(user, currentPassword, newPassword);

    }

    async submitOTP(phoneNumber:string, code:string): Promise<any> {
        try {
            const url = '/user/otp/';
            http.defaults.params = {should_autherize:false};
            await http.put(url, {code:code, phoneNumber:phoneNumber});
            return {error:undefined};
        } catch (e:any) {
            return {error:AuthServiceImpl.getErrorResponse(e?.data?.error)};
        }
    }

    async sendOTP(phoneNumber:string): Promise<any> {
        try {
            const url = '/otp/';
            http.defaults.params = {should_autherize:false};
            await http.post(url, {phoneNumber:phoneNumber});
            return {error:undefined};
        } catch (e:any) {
            return {error:AuthServiceImpl.getErrorResponse(e?.data?.error)};
        }
    }

    async logIn(phoneNumber: string, password: string): Promise<any> {
        try {
            await Auth.signIn(phoneNumber, password);
            return { error: undefined }
        } catch (error) {
            return {error:AuthServiceImpl.getErrorResponse(error)};
        }
    }

    async getUser() {
        return await Auth.currentAuthenticatedUser();
    }

    async getUserShowName() : Promise<string|undefined>{
        const user = await Auth.currentAuthenticatedUser({
            bypassCache: false
        });
        return user?.getSignInUserSession()?.getIdToken()?.payload?.name;
    }

    async facebookSignIn() {
        Auth.federatedSignIn({ provider: CognitoHostedUIIdentityProvider.Facebook });
    }

    async googleSignIn() {
        Auth.federatedSignIn({ provider: CognitoHostedUIIdentityProvider.Google });
    }

    private static getErrorResponse(error: any) {
        console.error("Auth service error : ",JSON.stringify(error));
        if (!error){
            return { code: AuthException.UNEXPECTED_ERROR, message: "Unexpected error !" }
        }
        if (error.code === AuthException.INVALID_PARAMETER ||
            error.code === AuthException.INVALID_PASSWORD ||
            error.code === AuthException.USERNAME_EXISTS ||
            error.code === AuthException.AMPLIFY_AUTH_ERROR ||
            error.code === AuthException.OTP_EXPIRED ||
            error.code === AuthException.OTP_INVALID ||
            error.code === AuthException.USER_NOT_FOUND ||
            error.code === AuthException.INVALID_USERNAME_OR_PASSWORD ||
            error.code === AuthException.USER_NOT_CONFIRMED) {
            return error;
        }
        return { code: AuthException.UNEXPECTED_ERROR, message: "Unexpected error !" }
    }

}

export default new AuthServiceImpl();
