import { logo } from '../../../assets';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link, useLocation } from "react-router-dom";
import { Form, FormControl, Col, InputGroup} from 'react-bootstrap';
import { faBars, faCartArrowDown, faSearch, faUser } from '@fortawesome/free-solid-svg-icons';
import React, {useEffect, useState} from "react";
import {CustomAuthModal} from "../auth-modal";
import {AuthService, AuthServiceImpl} from "../../../service/auth.service";

interface ILinkStateProps {
}

type Props = ILinkStateProps;

const Header: React.FC<Props> = ( ) => {

    const cartItemCount = 29;
    const location = useLocation();
    const authService: AuthService = new AuthServiceImpl();

    // states needed for auth modal
    const [showAuthModal, setShowAuthModal] = React.useState(false);
    const [userShowName, setUserShowName] = React.useState("");
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    // isLoggedIn and userShowName are need to show the Login button and name in header
    // if federated user, this should be checked and set as a hook, else set at success login/register
    useEffect( () => {
        async function setUserShowNameFunction() {
            try{
                const isLoggedIn: boolean = await authService.isLoggedIn();
                const name: string|undefined = await authService.getUserShowName();
                setIsLoggedIn(isLoggedIn);
                if (isLoggedIn){
                    setUserShowName(name?name:"not found");
                }
            }catch (error){
                setUserShowName("not found");
                setIsLoggedIn(false);
            }
        }
        setUserShowNameFunction();
    });

    return (
        <div className="header-bar">
            <CustomAuthModal setIsLoggedIn={setIsLoggedIn} showModalBool={showAuthModal} setUserShowName={setUserShowName} closeModalAction={() => { setShowAuthModal(false) }} />
            <input type="checkbox" id="nav-check" />
            <div className="header-logo">
                <Link to={'/'}>
                    <img
                        src={logo}
                        className="header-logo-img"
                        alt="Cakehub Logo"
                    />
                </Link>
            </div>
            <div className="header-nav">
                <div className="header-nav-item-list">
                    <Link className={"header-nav-item " + (location.pathname==='/' && "active")} to={'/'}> Home </Link>
                    <Link className={"header-nav-item " + (location.pathname==='/shop' && "active")} to={'/shop'}> Shops </Link>
                    <Link className="header-nav-item" to={'/shop'}> Categories </Link>
                    <Link className="header-nav-item" to={'/shop'}> Track Order </Link>
                    <Link className="header-nav-item" to={'/shop'}> Sell with Us </Link>
                    <Link className="header-nav-item" to={'/shop'}> Contact Us </Link>
                    <Link className="header-nav-item mobile-mini" to={'/shop'}> Cart </Link>
                    {isLoggedIn ? (
                        <Link className="header-nav-item mobile" to={'/shop'}> Your Account </Link>
                    ) : (
                        <Link className="header-nav-item mobile" to={'/shop'}> Log In </Link>
                    )
                    }
                </div>
                <div className="header-nav-search-bar">
                    <Col xs={11}>
                        <Form>
                            <InputGroup>
                                <InputGroup.Prepend className="header-search-box">
                                    <InputGroup.Text id="basic-addon1" className="header-nav-search-bar-prepend"><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
                                </InputGroup.Prepend>
                                <FormControl type="text" placeholder="Search" aria-label="Username" aria-describedby="basic-addon1" className="mr-sm-2 header-nav-search-bar-input" />
                            </InputGroup>
                        </Form>
                    </Col>
                    <Col xs={1} className="header-cart-logo web-mini">
                        {cartItemCount > 0 && <><span className='counter'>{cartItemCount}</span></>}
                        <FontAwesomeIcon icon={faCartArrowDown} />
                    </Col>
                </div>
            </div>
            {isLoggedIn ? (
                <div className="header-profile web">
                    <div className="header-profile-item"><FontAwesomeIcon icon={faUser} /> </div>
                    <div className="header-profile-item">
                        <div>{userShowName}</div>
                        <span>Welcome Back</span>
                    </div>
                </div>
            ) : (
                <div className="header-login web" onClick={ () => { setShowAuthModal(true); }}>
                    <div className="header-login-item"><FontAwesomeIcon icon={faUser} /> </div>
                    <div className="header-login-item">Log In</div>
                </div>
            )}
            <div className="header-mobile-bar">
                <label htmlFor="nav-check">
                    <FontAwesomeIcon icon={faBars} />
                </label>
            </div>
        </div>
    )
}

export { Header }