import { Row } from 'reactstrap';
import { IItemCardProps, ItemCard } from "../../organisms";
import styles from "./index.module.css";


interface PaginationProps {
    itemCards: IItemCardProps[]

}
const ItemList: React.FC<PaginationProps> = ({
    itemCards
}) => {
    return (
        <Row className={`d-flex ${styles.item_container}`}>
            {itemCards.map((item) => {
                return (
                    <ItemCard {...item} />
                );
            })}
        </Row>
    )
}

export {ItemList};
